.catalog-banner {
  padding-bottom: 25px;
  &__inner {
    background-image: url("../img/catalog-banner__img.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 25px;
    padding: 32px 50px;
    color: #ffffff;
    font-family: Unbounded;
  }
  &__text {
    font-size: 18px;
    line-height: 120%;
    letter-spacing: 0.45px;
    margin-top: 10px;
  }
  &__info  {
    font-size: 45px;
    line-height: 120%;
    background: linear-gradient(90deg, #FEDF4A 0%, #DF512D 49.45%, #FB8A50 100%, #FEDF4A 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-top: 20px;
  }
}
.product-catalog {
  padding-top: 25px;
  &__title {
    display: flex;
    align-items: center;
    i {
      flex-shrink: 0;
      width: 48px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: 15px;
    }
  }
  &__slider {
    margin-top: 30px;
  }
  &__card {
    min-height: 155px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #F8F8FA;
    border-radius: 20px;
    position: relative;
    &-title {
      padding: 15px 40px;
    }
    &-img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      flex-shrink: 0;
      max-width: 200px;
      img {
        max-width: 100%;
        height: 100%;
      }
    }
  }
  .menu-categories {
    margin-top: 24px;
    a:last-child {
      display: block;
    }
  }
}


@media screen and (max-width: 992px) {
  .catalog-banner {
    &__inner {
      padding: 28px 24px;
    }
    &__text {
      font-size: 16px;
    }
    &__info {
      font-size: 30px;
      margin-top: 100px;
    }
  }
  .product-catalog {
    &__card {
      min-height: 86px;
      &-img {

      }
    }
  }
}

@media screen and (max-width: 992px) {
  .product-catalog {
    &__card {

      &-title {
        font-size: 16px;
        padding: 16px;
      }
    }
  }
}