.text22 {
  font-size: 22px;
  line-height: 130%;
  letter-spacing: 0.88px;
}
.text25 {
  font-size: 25px;
  line-height: 130%;
  letter-spacing: 0.625px;
}
.text28 {
  font-size: 28px;
  line-height: 130%;
  font-weight: 500;
  letter-spacing: 0.56px;
}
.text30 {
  font-size: 30px;
  line-height: 120%;
  font-weight: 400;
}
.text35 {
  font-family: Unbounded;
  font-size: 35px;
  line-height: 120%;
  font-weight: 400;
  letter-spacing: 0.875px;
}
.text48 {
  font-family: Unbounded;
  font-size: 48px;
  line-height: 120%;
}
.font-unbounded {
  font-family: Unbounded;
}
.breadcrumbs {
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__item {
    font-size: 14px;
    line-height: 120%;
    color: #75767C;
    margin: 0 0 10px 0;
    &:hover {
      color: #A31D1C;
    }
    &--active {
      &:hover {
        color: #75767c;
      }
    }
    &-slash {
      &:hover {
        color: #75767c;
      }
    }
    &-img {
      width: 16px;
      margin-right: 12px;
      img {
        width: 100%;
      }
    }
  }
}
.page-top {
  padding: 35px 0;
  &__link {
    display: flex;
    align-items: center;
    margin-top: 6px;
    i {
      flex-shrink: 0;
      margin-right: 12px;
    }
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 500;
    }
    &:hover {
      i svg path {
        fill: #A31D1C;
      }
      span {
        color: #A31D1C;
      }
    }
  }
  &__wrap {
    margin-top: 22px;
  }
  &__items {
    width: calc(100% + 22px);
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
  }
  &__item {
    width: 164px;
    min-height: 68px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    border: 1px solid #F0F0F4;
    border-radius: 8px;
    padding: 16px;
    margin: 0 22px 16px 0;
    &:hover {
      border-color: #A31D1C;
      .page-top__item-text {
        color: #A31D1C;
      }
    }
    &--active {
      background: #F0F0F4;
      &:hover {
        border: 1px solid #F0F0F4;
        .page-top__item-text {
          color: #000000;
        }
      }
    }
    &-icon {
      flex-shrink: 0;
      max-width: 48px;
      margin-right: 6px;
      img {
        max-width: 100%;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 140%;
    }
  }
  .bg-red {
    background: #A31D1C;
    border: 1px solid #A31D1C;
    .page-top__item-text {
      color: #ffffff;
    }
    .page-top__item-text {
      color: #ffffff;
    }
  }
  &__mob {
    display: none;
  }
}
.nav {
  display: grid;
  grid-template-columns: 100%;
  gap: 15px;
  padding: 50px 0;
  li {
    font-size: 20px;
    line-height: 120%;
    font-weight: 500;
    a {
      &:hover {
        color: #A31D1C;
      }
    }
  }
  &-btn {
    width: 300px;
    &:not(:first-child) {
      margin-top: 15px;
    }
  }
}
.red-btn {
  height: 61px;
  background: #A31D1C;
  border-radius: 10px;
  span {
    font-size: 20px;
    line-height: 120%;
    font-weight: 600;
    color: #ffffff;
  }
  &:hover {
    background: #8b1211;
  }
}
.medium-text {
  font-weight: 500;
}
.semibold-text {
  font-weight: 600;
}
.bold-text {
  font-weight: 700;
}
.red-text {
  color: #A31D1C !important;
}
.wrap {
  overflow: hidden;
}
.search {
  position: relative;
  input {
    width: 100%;
    height: 44px;
    background: #F0F0F4;
    border-color: transparent;
    border-radius: 8px;
    padding: 5px 15px 5px 40px;
    font-size: 14px;
    line-height: 120%;
    color: #000000;
    &::placeholder {
      color: #75767C;
    }
  }
  img {
    position: absolute;
    top: 50%;
    left: 15px;
    transform: translate(0, -50%);
  }
}
.swiper-button-prev {
  &:after {
    display: none;
  }
}
.swiper-button-next {
  &:after {
    display: none;
  }
}
.tabs {
  .tab__content {
    display: none;
    &--active {
      display: block;
    }
  }
}
.modal {
  background: rgba(0, 0, 0, 0.5);;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1010;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 872px;
    max-width: 100%;
    position: relative;
    background: #ffffff;
    border-radius: 20px;
    padding: 60px 40px 30px;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
      max-width: calc(100% - 30px);
    }
  }
  &__dialog2 {
    width: 469px;
    padding: 40px;
  }
  &__content2 {
    text-align: center;
  }
  &__close {
    position: absolute;
    top: 15px;
    right: 35px;
    background: transparent;
    width: 18px;
    padding: 0;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__title {
    font-size: 25px;
    line-height: 120%;
  }
  &__text {
    font-size: 18px;
    line-height: 140%;
    color: #696969;
    margin-top: 15px;
  }
  &__checkboxes {
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 36px / 3));
    gap: 18px;
    border-radius: 20px;
    margin-top: 20px;
  }
  &__checkbox {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      margin: 0;
      opacity: 0;
      padding: 0;
      cursor: pointer;
      &:checked ~ .modal__checkbox-desc {
        background: #A31D1C;
        .modal__checkbox-title {
          color: #ffffff;
        }
      }
    }
    &-desc {
      display: flex;
      align-items: center;
      background: #FFF;
      border: 0.5px solid #A31D1C;
      border-radius: 10px;
      padding: 10px 8px;
      transition: all 0.5s;
    }
    &-img {
      flex-shrink: 0;
      width: 35px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 15px;
      img {
        width: 100%;
      }
    }
    &-title {
      font-size: 20px;
      line-height: 140%;
    }
  }
  &__form {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 30px;
    &-group {
      border: none;
      margin: 0;
      padding: 0;
      input {
        width: 100%;
        height: 56px;
        background: #FFF;
        border: 1px solid #DFDFDF;
        border-radius: 5px;
        padding: 5px 20px;
      }
    }
    &-btn {
      width: 100%;
      height: 56px;
      background: #F0F0F4;
      border-radius: 10px;
      span {
        font-size: 20px;
        line-height: 120%;
      }
    }
    &-info {
      max-width: 307px;
      margin: 0 auto;
      font-size: 14px;
      line-height: 120%;
      color: #75767C;
    }
  }
  &__info {
    display: flex;
    align-items: center;
    margin-top: 10px;
    i {
      width: 17px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      margin-right: 7px;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
      line-height: 120%;
    }
  }
  &__flex {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: calc(100% - 330px);
  }
  &__right {
    width: 300px;
    margin-left: 30px;
  }
  &__group {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 15px;
  }
  &__item {
    display: flex;
    &-copy {
      display: flex;
      i {
        width: 20px;
        margin-left: 10px;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 16px;
        line-height: 120%;
        color: #64AD01;
      }
    }
    &-icon {
      flex-shrink: 0;
      width: 20px;
      margin-right: 15px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 18px;
      line-height: 130%;
      color: #696969;
    }
    &-list {
      width: calc(100% + 24px);
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      margin-top: 12px;
      li {
        font-size: 16px;
        line-height: 130%;
        color: #696969;
        margin: 0 24px 10px 0;
        position: relative;
        padding-left: 15px;
        &:after {
          content: '';
          position: absolute;
          top: 7px;
          left: 0;
          width: 7px;
          height: 7px;
          background: #A31D1C;
          border-radius: 50%;
        }
      }
    }
  }
  &__subtitle {
    font-size: 22px;
    line-height: 120%;
  }
  .gallery {
    &__big {
      .swiper-slide {
        img {
          border-radius: 10px;
        }
      }
    }
    &__small {
      &-wrapp {
        margin-top: 15px;
      }
      .swiper-slide {
        height: 50px;
        img {
          border-radius: 5px;
        }
      }
    }
    .swiper-slide {
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
}
.up {
  &--active {
    opacity: 1 !important;
    pointer-events: all !important;
  }
}
.products {
  &__slider {
    position: relative;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    padding: 5px;
  }
  .swiper-button-prev {
    left: -80px;
    width: 29px;
    svg {
      width: 100%;
      height: auto;
    }
    &:hover {
      svg path {
        fill: #d5201f;
      }
    }
  }
  .swiper-button-next {
    right: -80px;
    width: 29px;
    svg {
      width: 100%;
      height: auto;
    }
    &:hover {
      svg path {
        fill: #d5201f;
      }
    }
  }
}
.pagination {
  &__items {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__item {
    font-size: 18px;
    line-height: 120%;
    color: #838383;
    &--active {
      font-weight: 600;
      color: #000000;
    }
    &:not(:first-child) {
      margin-left: 15px;
    }
  }
  &__prev {
    font-size: 18px;
    line-height: 120%;
    color: #838383;
  }
  &__next {
    font-size: 18px;
    line-height: 120%;
    color: #838383;
    margin-left: 15px;
  }
}
.favorite {
  &--active {
    svg path {
      fill: #A31D1C;
    }
  }
  &:hover {
    svg path {
      fill: #A31D1C;
    }
  }
}
.collapse--active {
  .collapse__caret {
    transform: rotate(180deg);
  }
}
.ratings {
  position: relative;
  font-size: 40px;
  display: inline-block;
  &:before {
    content: '';
    display: block;
    color: #D9D9D9;
  }
  &__wrap {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row-reverse;
    overflow: hidden;
  }
  .rating {
    position: absolute;
    top: 0;
    left: 0;
    width: 0%;
    height: 0%;
    opacity: 0;
    visibility: hidden;
  }
  .rating:checked ~ .rating__label {
    color: #A31D1C;
  }
  .rating__label {
    flex: 0 0 20%;
    height: 100%;
    color: #D9D9D9;
    cursor: pointer;
    &:before {
      content: '';
      display: block;
      transition: all 0.3s;
    }
  }
  .rating__label:hover,
  .rating__label:hover ~ .rating__label,
  .rating__label:checked ~ .rating__label {
    color: #A31D1C;
  }
}


@media screen and (max-width: 1550px) {
  .products {
    .swiper-button-prev {
      left: -10px;
    }
    .swiper-button-next {
      right: -10px;
    }
  }
}

@media screen and (max-width: 1201px) {
  .products {
    .swiper-button-prev {
      display: none;
    }
    .swiper-button-next {
      display: none;
    }
  }
}

@media screen and (max-width: 992px) {
  .text22 {
    font-size: 18px;
  }
  .text25 {
    font-size: 20px;
  }
  .text28 {
    font-size: 22px;
  }
  .text30 {
    font-size: 20px;
  }
  .text35 {
    font-size: 28px;
  }
  .text48 {
    font-size: 30px;
  }
  .page-top {
    &__items {
      width: calc(100% + 15px);
      flex-wrap: nowrap;
      overflow-x: auto;
      padding-bottom: 10px;
      &::-webkit-scrollbar {
        height: 3px;
      }
      &::-webkit-scrollbar-track {
        background: transparent;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #B4B4B4;
        border-radius: 3px;
      }
    }
    &__item {
      flex-shrink: 0;
      width: auto;
      min-height: 100%;
      background: #F0F0F4;
      margin: 0;
      padding: 14px;
      &:not(:last-child) {
        margin-right: 12px;
      }
      &:last-child {
        margin-right: 12px;
      }
      &--active {
        display: none;
      }
      &:hover {
        border-color: #F0F0F4;
        .page-top__item-text {
          color: #000000;
        }
      }
      &-text {
        font-size: 16px;
      }
    }
    .bg-red {
      display: none;
      &:hover {
        border-color: #A31D1C;
        .page-top__item-text {
          color: #ffffff;
        }
      }
    }
    &__mob {
      display: flex;
      align-items: stretch;
      margin-bottom: 20px;
      .page-top__item {
        max-width: 132px;
        padding: 8px 14px;
        &-icon {
          max-width: 36px;
        }
        &--active {
          display: flex;
        }
      }
      .bg-red {
        display: flex;
      }
    }
  }
  .red-btn {
    height: 54px;
    span {
      font-size: 18px;
    }
  }
  .modal {
    &__dialog {
      padding: 50px 16px 30px;
    }
    &__dialog2 {
      padding: 40px 16px 30px;
    }
    &__text {
      font-size: 16px;
    }
    &__form {
      &-btn {
        font-size: 18px;
      }
    }
    &__checkboxes {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
      gap: 10px;
    }
    &__checkbox-title {
      font-size: 16px;
    }
  }
}

@media screen and (max-width: 768px) {
  .modal {
    &__flex {
      flex-direction: column;
    }
    &__left {
      width: 100%;
    }
    &__right {
      order: -1;
      width: 100%;
      margin: 0 0 30px 0;
    }
    .gallery {
      &__small {
        .swiper-slide {
          height: auto;
        }
      }
    }
  }
  .pagination {
    &__item {
      font-size: 16px;
      &:not(:first-child) {
        margin-left: 10px;
      }
    }
    &__prev {
      font-size: 16px;
    }
    &__next {
      font-size: 16px;
      margin-left: 10px;
    }
  }
}

@media screen and (max-width: 767px) {
  .products {
    .swiper-slide {
      width: 174px !important;
      opacity: 1 !important;
      pointer-events: all !important;
    }
  }
}


@media screen and (max-width: 576px) {
  .page-top {
    padding: 25px 0 15px 0;
    &__link {
      display: none;
    }
  }
  .text35 {
    font-size: 20px;
  }
  .text48 {
    font-size: 20px;
  }
  .modal {
    &__checkbox-img {
      width: 20px;
      margin-right: 8px;
    }
    &__checkbox-title {
      font-size: 12px;
    }
  }
  .insert-to {
    max-width: 120px !important;
  }
}

@media screen and (max-width: 360px) {
  .modal {
    &__checkbox {
      &-desc {
        display: block;
      }
      &-img {
        margin: 0 0 8px 0;
      }
    }
  }
}