.contacts-main {
  padding-bottom: 25px;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-image: url("../img/contacts-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 30px;
    padding: 80px 24px;
    color: #FFF;
  }
  &__title {
    font-size: 45px;
    line-height: 120%;
  }
  &__text {
    max-width: 740px;
    margin: 27px auto 0;
  }
  &__btn {
    width: 383px;
    margin: 70px auto 0;
  }
  &__links {
    grid-template-columns: 100%;
    gap: 36px;
    display: none;
  }
  &__link {
    display: inline-block;
    font-size: 22px;
    line-height: 120%;
    letter-spacing: 0.55px;
    text-decoration-line: underline;
    &:hover {
      color: #A31D1C;
    }
  }
}
.contacts-clients {
  padding: 25px 0 40px 0;
  &__title {
    text-align: center;
  }
  &__items {
    width: calc(100% + 50px);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 30px;
  }
  &__item {
    max-width: 553px;
    display: flex;
    align-items: center;
    margin: 0 50px 50px 0;
    &-icon {
      flex-shrink: 0;
      width: 80px;
      margin-right: 30px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 22px;
      line-height: 140%;
      a {
        display: block;
        margin-top: 15px;
      }
    }
  }
  &__bottom {
    margin-top: 80px;
  }
}
.contacts {
  &__grid {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 50px / 2));
    gap: 50px;
    &:not(:first-child) {
      margin-top: 50px;
    }
  }
  &__item {
    #map2,
    #map3 {
      width: 100%;
      height: 364px;
      .ymaps-2-1-79-map {
        width: 100% !important;
        height: 100% !important;
      }
      .ymaps-2-1-79-inner-panes {
        border-radius: 10px;
      }
    }
  }
  &__map {
    &-desc {
      margin-top: 32px;
    }
    &-title {
      font-size: 22px;
      line-height: 140%;
    }
    &-text {
      font-size: 16px;
      line-height: 140%;
      color: #696969;
      margin-top: 9px;
    }
    &-info {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 15px;
    }
  }
  &__select {
    .nice-select {
      max-width: 170px;
      background: transparent;
      border: none;
      padding: 0 15px 0 0;
      width: auto;
      margin-left: 15px;
      &:after {
        right: 0;
        top: 50%;
        transform: translate(0, -50%);
        width: 9px;
        height: 5px;
        background-image: url("../img/select-arrow.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
      .current {
        font-size: 18px;
        line-height: 120%;
        font-weight: 500;
        letter-spacing: 0.45px;
      }
      .list {
        li {
          font-size: 14px;
          padding: 10px 5px;
        }
      }
    }
  }
  &__connection {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 20px;
    &-item {
      display: inline-flex;
      &:hover {
        i svg path {
          fill: #A31D1C;
        }
        span {
          color: #A31D1C;
        }
      }
      i {
        width: 20px;
        flex-shrink: 0;
        display: flex;
        align-items: center;
        margin-right: 10px;
        svg {
          max-width: 100%;
          height: auto;
        }
      }
      span {
        font-size: 18px;
        line-height: 120%;
      }
    }
  }
  &__address {
    &-title {
      font-size: 18px;
      line-height: 120%;
      color: #696969;
    }
    &-text {
      display: inline-flex;
      align-items: center;
      margin-top: 15px;
      i {
        flex-shrink: 0;
        width: 15px;
        display: flex;
        align-items: center;
        margin-right: 10px;
        img {
          width: 100%;
          height: auto;
        }
      }
      span {
        font-size: 20px;
        line-height: 120%;
      }
      &:hover {
        color: #A31D1C;
      }
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    margin-top: 30px;
  }
  &__social {
    width: 43px;
    position: relative;
    &:not(:first-child) {
      margin-left: 43px;
    }
    svg {
      width: 100%;
      height: auto;
      transition: all 0.3s;
    }
    svg:nth-child(2) {
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      opacity: 0;
      pointer-events: none;
    }
    &:hover {
      svg:nth-child(2) {
        opacity: 1;
        pointer-events: all;
      }
    }
  }
  &__chart {
    margin-top: 30px;
    &-title {
      font-size: 18px;
      line-height: 120%;
      color: #696969;
    }
    &-items {
      width: calc(100% + 35px);
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      margin-top: 20px;
    }
    &-item {
      display: flex;
      align-items: center;
      margin: 0 35px 35px 0;
    }
  }
  &__day {
    height: 44px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #A31D1C;
    border-radius: 10px;
    padding: 3px 12px;
    font-size: 18px;
    line-height: 120%;
    color: #ffffff;
  }
  &__day2 {
    background: #75767C;
  }
  &__time {
    font-size: 18px;
    line-height: 120%;
    margin-left: 15px;
  }
  &__item-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
  }
  &__links {
    display: flex;
    align-items: center;
  }
  &__link {
    width: 140px;
    height: 44px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F9F9F9;
    border: 1px solid transparent;
    border-radius: 10px;
    padding: 5px;
    font-size: 18px;
    line-height: 120%;
    &:not(:first-child) {
      margin-left: 12px;
    }
    &:hover {
      background: transparent;
      border: 1px solid #A31D1C;
    }
    &--active {
      background: transparent;
      border: 1px solid #A31D1C;
    }
  }
  &__text {
    font-size: 18px;
    line-height: 140%;
    margin-top: 20px;
  }
}
.stars {
  display: flex;
  align-items: center;
  &__view {
    display: flex;
    align-items: center;
  }
  &__item {
    width: 22px;
    &:not(:first-child) {
      margin-left: 5px;
    }
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__rate {
    font-size: 20px;
    line-height: 120%;
    font-weight: 500;
    margin-left: 7px;
  }
}
.contacts-suppliers {
  padding: 25px 0 0 0;
  &__title {
    text-align: center;
  }
  &__top {
    margin-top: 30px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 80px / 3));
    gap: 40px;
  }
  &__card {
    min-height: 220px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: #FBE8DB;
    border-radius: 25px;
    padding: 32px 24px;
    &:nth-child(even) {
      background: #F0DBDB;
    }
    &-text {
      line-height: 160%;
      a {
        display: block;
        margin-top: 6px;
      }
    }
  }
  &__bottom {
    margin-top: 50px;
  }
  &__subtitle {
    text-align: center;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 120px / 3));
    gap: 40px 60px;
    margin-top: 44px;
  }
  &__item {
    display: flex;
    align-items: center;
    max-width: 390px;
    margin: 0 auto;
    &-icon {
      flex-shrink: 0;
      width: 50px;
      margin-right: 15px;
    }
    &-text {
      font-size: 18px;
      line-height: 120%;
      a {
        display: block;
        font-size: 20px;
        line-height: 120%;
        margin-top: 10px;
      }
    }
  }
}


@media screen and (max-width: 1200px) {
  .contacts-clients {
    &__items {
      width: 100%;
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 50px / 2));
      gap: 50px;
    }
    &__item {
      max-width: 100%;
      margin: 0;
    }
  }
  .contacts-suppliers {
    &__card {
      &-text {
        font-size: 20px;
      }
    }
    &__items {
      grid-template-columns: repeat(3, calc(100% / 3 - 80px / 3));
      gap: 40px;
    }
  }
  .contacts {
    &__item-top {
      display: block;
    }
    &__links {
      margin: 30px 0 0 0;
    }
  }
}

@media screen and (max-width: 992px) {
  .contacts-main {
    &__title {
      font-size: 30px;
    }
    &__text {
      font-size: 20px;
      margin-top: 20px;
    }
    &__btn {
      margin-top: 40px;
    }
  }
  .contacts-clients {
    &__item {
      &-icon {
        width: 60px;
      }
      &-text {
        font-size: 18px;
      }
    }
    &__bottom {
      margin-top: 50px;
    }
  }
  .contacts {
    &__grid {
      grid-template-columns: 100%;
      gap: 30px;
    }
  }
  .contacts-suppliers {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
      gap: 20px;
    }
    &__card {
      &-text {
        font-size: 18px;
      }
    }
    &__items {
      grid-template-columns: repeat(2, calc(50% - 30px / 2));
      gap: 30px;
      margin-top: 30px;
    }
    &__item {
      max-width: 100%;
      margin: 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .contacts-main {
    &__inner {
      min-height: 315px;
      align-items: start;
      padding: 40px 30px;
    }
    &__content {
      display: none;
    }
    &__links {
      display: grid;
    }
  }
  .contacts-clients {
    &__items {
      grid-template-columns: 100%;
      gap: 30px;
    }
    &__item {
      max-width: 343px;
    }
  }
  .contacts-suppliers {
    &__cards {
      grid-template-columns: 100%;
    }
    &__card {
      min-height: 116px;
    }
    &__items {
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .contacts {
    &__item {
      #map {
        height: 232px;
      }
    }
    &__map {
      &-desc {
        margin-top: 20px;
      }
      &-title {
        font-size: 20px;
      }
    }
    &__select {
      .nice-select {
        .current {
          font-size: 16px;
        }
      }
    }
    &__address {
      &-title {
        font-size: 16px;
      }
      &-text {
        span {
          font-size: 18px;
        }
      }
    }
    &__chart {
      &-title {
        font-size: 16px;
      }
      &-items {
        width: calc(100% + 15px);
      }
      &-item {
        margin: 0 15px 15px 0;
      }
    }
    &__day {
      font-size: 16px;
    }
    &__time {
      font-size: 14px;
      margin-left: 12px;
    }
    &__item-top {
      margin-bottom: 20px;
    }
    &__text {
      font-size: 16px;
      margin-top: 15px;
    }
  }
  .stars {
    &__item {
      width: 15px;
    }
    &__rate {
      font-size: 18px;
    }
  }
}

@media screen and (max-width: 360px) {
  .contacts {
    &__map-info {
      flex-direction: column;
      align-items: start;
    }
    &__select {
      .nice-select {
        margin: 15px 0 0 0;
      }
    }
  }
}