.recipes {
  padding-bottom: 25px;
  &__slider {
    position: relative;
    &-btns {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      margin-top: 13px;
    }
  }
  &__swiper-wrapper {
    align-items: stretch;
  }
  .swiper-button-prev {
    position: static;
    z-index: 2;
    width: 30px;
    height: 30px;
    background: #A31D1C;
    border-radius: 50%;
    margin-top: 0;
    &:after {
      display: none;
    }
  }
  .swiper-button-next {
    position: static;
    z-index: 2;
    width: 30px;
    height: 30px;
    background: #A31D1C;
    border-radius: 50%;
    margin-top: 0;
    margin-left: 12px;
    &:after {
      display: none;
    }
  }
  .swiper-button-disabled {
    opacity: 0.5;
  }
  .swiper-pagination {
    z-index: 1;
    bottom: 7px;
    &-bullet {
      &-active {
        background: #DF512D;
      }
    }
  }
  &__search {
    margin-top: 38px;
  }
}
.recipe-card {
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #EEEEEE;
  border-radius: 20px;
  &__title {
    font-size: 20px;
    line-height: 120%;
    font-weight: 500;
    padding: 22px 16px;
  }
  &__img {
    display: flex;
    justify-content: flex-end;
    img {
      max-width: 100%;
    }
  }
}
.popular-recipes {
  padding: 25px 0;
  &__slider {
    position: relative;
    margin-top: 50px;
  }
  .swiper-button-prev {
    top: 40%;
    left: -60px;
  }
  .swiper-button-next {
    top: 40%;
    right: -60px;

  }
}
.popular-recipe__card {
  position: relative;
  .popular-recipe__top-text {
    position: absolute;
    top: 15px;
    left: 15px;
    background: rgba(255, 255, 255, 0.77);
    border-radius: 20px;
    padding: 4px 15px;
    font-size: 16px;
    line-height: 120%;
    color: #585858;
    letter-spacing: 0.96px;
  }
  .popular-recipe__top-btns {
    position: absolute;
    top: 15px;
    right: 15px;
    display: grid;
    grid-template-columns: 100%;
    gap: 7px;
  }
  .popular-recipe__top-btn {
    width: 43px;
    height: 43px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ffffff;
    border-radius: 50%;
    svg {
      max-width: 20px;
    }
    &:hover {
      svg path {
        fill: #A31D1C;
        stroke: #A31D1C;
      }
    }
  }
  &-img {
    img {
      width: 100%;
      border-radius: 20px;
    }
  }
  &-desc {
    margin-top: 20px;
  }
  &-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
  }
  .popular-recipe__info-left {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  .popular-recipe__info-right {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-left: 10px;
  }
  .popular-recipe__rating,
  .popular-recipes__portion,
  .popular-recipes__time,
  .popular-recipes__views,
  .popular-recipe__comments {
    display: flex;
    align-items: center;
    i {
      flex-shrink: 0;
      max-width: 20px;
      margin-right: 4px;
      svg {
        max-width: 100%;
      }
    }
    span {
      font-size: 18px;
      line-height: 120%;
      letter-spacing: 1.08px;
    }
  }
  .popular-recipe__rating {
    margin-right: 10px;
  }
  .popular-recipes__portion {
    margin-right: 10px;
  }
  .popular-recipes__views {
    color: #6C6C6C;
    margin-right: 10px;
  }
  .popular-recipe__comments {
    color: #6C6C6C;
  }
}
.video-recipes {
  padding: 25px 0 0 0;
  &__slider {
    position: relative;
    margin-top: 30px;
  }
  .swiper-button-prev {
    left: -60px;
  }
  .swiper-button-next {
    right: -60px;

  }
}
.video-recipe__card {
  min-height: 473px;
  height: 100%;
  position: relative;
  &-btns {
    position: absolute;
    z-index: 3;
    top: 15px;
    right: 15px;
    display: flex;
    align-items: center;
    &--active {
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
  }
  &-btn {
    max-width: 30px;
    &:not(:first-child) {
      margin-left: 8px;
    }
    svg {
      max-width: 100%;
    }
  }
  &-title{
    position: absolute;
    z-index: 2;
    left: 0;
    right: 0;
    bottom: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.78);
    padding: 24px 12px;
    pointer-events: none;
    &--active {
      opacity: 0;
      transition: all 0.3s;
    }
  }
  .video {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    object-fit: cover;
  }
  .play {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 123px;
    cursor: pointer;
    &--active {
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    img {
      width: 100%;
    }
  }
}


@media screen and (max-width: 1450px) {
  .popular-recipes {
    .swiper-button-prev {
      left: -15px;
    }
    .swiper-button-next {
      right: -15px;
    }
  }
  .popular-recipe__card {
    &-info {
      display: block;
    }
    .popular-recipe__info-right {
      margin: 12px 0 0 0;
    }
  }
  .video-recipes {
    .swiper-button-prev {
      left: -15px;
    }
    .swiper-button-next {
      right: -15px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .video-recipe__card {
    min-height: 400px;
  }
}

@media screen and (max-width: 992px) {
  .recipes {
    &__slider {
      &-nav {
        display: none;
      }
    }
    &__swiper {
      overflow: unset;
    }
  }
  .recipe-card {
    &__title {
      font-size: 16px;
    }
    &__img {
      max-width: 100px;
      margin: 0 0 0 auto;
      &2 {
        max-width: 170px;
      }
    }
  }
  .popular-recipes {
    &__slider {
      margin-top: 30px;
      &-nav {
        display: none;
      }
    }
    &__swiper {
      overflow: unset;
    }
  }
  .popular-recipe__card {
    .popular-recipe__top-text {
      font-size: 14px;
    }
    .popular-recipe__top-btn {
      width: 25px;
      height: 25px;
      svg {
        max-width: 13px;
      }
    }
    &-desc {
      margin-top: 15px;
    }
    &-title {
      font-size: 18px;
    }
    &-info {
      margin-top: 15px;
    }
    .popular-recipe__rating,
    .popular-recipes__portion,
    .popular-recipes__time,
    .popular-recipes__views,
    .popular-recipe__comments {
      span {
        font-size: 16px;
      }
    }
  }
  .video-recipes {
    &__slider {
      &-nav {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .video-recipe__card {
    min-height: 350px;
    .play {
      width: 60px;
    }
  }
}

@media screen and (max-width: 576px) {
  .video-recipe__card {
    min-height: 210px;
    &-title {
      padding: 12px;
    }
    .play {
      display: none;
    }
  }
}