.making {
  &__tabs2 {
    display: flex;
    align-items: center;
    margin-top: 22px;
  }
  &__tab2 {
    width: 310px;
    height: 43px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: #F9F9F9;
    border: 1px solid transparent;
    border-radius: 5px;
    font-size: 18px;
    line-height: 120%;
    cursor: pointer;
    &--active {
      background: #ffffff;
      border: 1px solid #000000;
    }
    &:not(:first-child) {
      margin-left: 27px;
    }
  }
  &__content {
    display: none;
    &--active {
      display: block;
    }
  }
  &__tabs {
    margin-top: 40px;
    &-head {
      display: flex;
      justify-content: space-between;
      align-items: stretch;
      overflow-x: auto;
      padding-bottom: 10px;
      &::-webkit-scrollbar {
        height: 3px;
      }
      &::-webkit-scrollbar-track {
        background: transparent;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #B4B4B4;
        border-radius: 3px;
      }
    }
    &-body {
      margin-top: 50px;
    }
  }
  &__tab {
    flex-shrink: 0;
    width: 305px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #F9F9F9;
    border: 1px solid transparent;
    border-radius: 15px;
    padding: 20px;
    cursor: pointer;
    &:not(:last-child) {
      margin-right: 15px;
    }
    &-title {
      display: flex;
      align-items: center;
      i {
        flex-shrink: 0;
        width: 35px;
        margin-right: 10px;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 20px;
        line-height: 120%;
      }
    }
    &-price {
      margin-top: 40px;
      span {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        background: #A31D1C;
        border-radius: 5px;
        min-width: 110px;
        height: 30px;
        padding: 3px 10px;
        color: #ffffff;
        font-size: 18px;
        line-height: 120%;
      }
    }
  }
  .tab {
    &--active {
      background: #ffffff;
      border: 1px solid #000;
    }
  }
  &__items {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: 340px;
    margin-right: 40px;
    .making__checkboxes {
      margin-top: 0;
    }
  }
  &__right {
    width: calc(100% - 380px);
    display: flex;
    justify-content: space-between;
  }
  &__mob-btn {
    width: 202px;
    height: 35px;
    background: #ffffff;
    border: 1px solid #A31D1C;
    border-radius: 7px;
    margin-left: auto;
    margin-bottom: 16px;
    display: none;
    span {
      font-size: 16px;
      line-height: 120%;
      color: #262626;
    }
    &:hover {
      background: #A31D1C;
      span {
        color: #ffffff;
      }
    }
  }
  &__item {
    &-top {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
    }
    &-title {
      font-size: 22px;
      line-height: 120%;
      margin-right: 24px;
      margin-bottom: 10px;
    }
    &-entrance {
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      i {
        flex-shrink: 0;
        width: 18px;
        margin-right: 6px;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 18px;
        line-height: 120%;
      }
    }
    &-address {
      width: calc(100% - 470px);
    }
    &-address2 {
      width: 340px;
      margin-right: 40px;
    }
    &-map {
      width: 430px;
      margin-left: 40px;
      .making__item-top {
        display: none;
      }
    }
    &-map2 {
      width: calc(100% - 380px);
      margin-left: 0;
    }
    &-btn {
      width: 202px;
      height: 35px;
      background: #ffffff;
      border: 1px solid #A31D1C;
      border-radius: 7px;
      margin-left: auto;
      margin-bottom: 16px;
      span {
        font-size: 16px;
        line-height: 120%;
        color: #262626;
      }
      &:hover {
        background: #A31D1C;
        span {
          color: #ffffff;
        }
      }
    }
    &-detailed {
      font-size: 16px;
      line-height: 120%;
      color: #6C6C6C;
      margin-bottom: 10px;
    }
    &-wrap {
      margin-top: 35px;
    }
    &-group {
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
  }
  #map,
  #map2,
  #map3,
  #map4,
  #map1\.1,
  #map2\.1,
  #map3\.1,
  #map4\.1 {
    width: 100%;
    height: 270px;
    overflow: hidden;
    margin-top: 35px;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
    }
    .ymaps-2-1-79-inner-panes {
      border-radius: 10px;
    }
  }
  #map,
  #map3,
  #map4,
  #map1\.1,
  #map3\.1,
  #map4\.1 {
    height: 350px;
  }
  &__form {
    display: grid;
    grid-template-columns: 100%;
    gap: 28px;
    margin-top: 35px;
    &-group {
      border: none;
      margin: 0;
      padding: 0;
      input {
        width: 100%;
        height: 44px;
        background: #ffffff;
        border: 1px solid #DFDFDF;
        border-radius: 5px;
        padding: 5px 20px;
        font-size: 16px;
        line-height: 120%;
        &::placeholder {
          color: #75767C;
        }
      }
      textarea {
        width: 100%;
        height: 94px;
        background: #ffffff;
        border: 1px solid #DFDFDF;
        border-radius: 5px;
        padding: 15px 20px;
        font-size: 16px;
        line-height: 120%;
        resize: none;
        margin-top: 15px;
        &::placeholder {
          color: #75767C;
        }
      }
    }
    &-subtitle {
      font-size: 16px;
      line-height: 120%;
      letter-spacing: 0.4px;
    }
    &-info {
      display: flex;
      align-items: center;
      margin-top: 15px;
      i {
        flex-shrink: 0;
        width: 15px;
        margin-right: 10px;
        svg {
          width: 100%;
          height: auto;
        }
      }
      span {
        font-size: 16px;
        line-height: 120%;
        color: #5F5F5F;
        letter-spacing: 0.4px;
        text-decoration-line: underline;
      }
    }
  }
  .map__text {
    margin-top: 0;
  }
  &__checkboxes {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 15px;
  }
  &__checkbox {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      margin: 0;
      cursor: pointer;
      opacity: 0;
      &:checked ~ label {
        &:before {
          opacity: 1;
          pointer-events: all;
        }
      }
    }
    label {
      display: block;
      font-size: 18px;
      line-height: 120%;
      position: relative;
      padding-left: 26px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 18px;
        height: 18px;
        border: 1px solid #E4E4E4;
        border-radius: 3px;
      }
      &:before {
        content: '';
        position: absolute;
        z-index: 1;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 18px;
        height: 18px;
        background: #A31D1C;
        border: 1px solid #A31D1C;
        border-radius: 3px;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
    }
  }
  &__checkbox-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__calendar {
    margin-left: 16px;
    input {
      width: 183px;
      height: 34px;
      background-color: #FFF;
      border: 1px solid #DFDFDF;
      border-radius: 5px;
      background-image: url("../img/calendar-icon.svg");
      background-repeat: no-repeat;
      background-position: top 50% right 8px;
      background-size: 17px 20px;
      font-size: 14px;
      line-height: 120%;
      padding: 5px 10px;
      &::placeholder {
        color: #75767C;
      }
    }
    input[type='date']::-webkit-calendar-picker-indicator {
      opacity: 0;
    }
  }
  &-payment {
    margin-top: 50px;
    &__title {
      font-size: 22px;
      line-height: 120%;
    }
    &__checkboxes {
      width: calc(100% + 40px);
      display: flex;
      flex-wrap: wrap;
      align-items: stretch;
      margin-top: 30px;
    }
    &__checkbox {
      width: calc(50% - 40px);
      position: relative;
      margin: 0 40px 20px 0;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        margin: 0;
        cursor: pointer;
        opacity: 0;
        &:checked ~ .making-payment__checkbox-desc {
          background: #ffffff;
          border: 1px solid #000000;
        }
      }
      &-desc {
        width: 100%;
        height: 100%;
        min-height: 82px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #F9F9F9;
        border: 1px solid transparent;
        border-radius: 10px;
        padding: 10px 16px;
      }
      &-icon {
        flex-shrink: 0;
        display: flex;
        align-items: center;
        max-width: 40px;
        img {
          max-width: 100%;
        }
      }
      &-icon2 {
        max-width: 109px;
      }
      &-title {
        font-size: 16px;
        line-height: 120%;
        margin-left: 15px;
      }
    }
    &__info {
      display: flex;
      align-items: center;
      margin-top: 10px;
      i {
        flex-shrink: 0;
        width: 16px;
        margin-right: 7px;
        img {
          width: 100%;
        }
      }
      p {
        font-size: 16px;
        line-height: 140%;
        color: #75767C;
      }
    }
    &__btn {
      width: 440px;
      height: 65px;
      margin: 50px auto 0;
    }
  }
}
.shipping-methods {
  margin-top: 30px;
  &__title {
    font-size: 22px;
    line-height: 120%;
  }
  &__group {
    display: flex;
    align-items: center;
  }
  &__info {
    display: flex;
    align-items: center;
    margin-left: 24px;
    i {
      flex-shrink: 0;
      width: 18px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 8px;
      img {
        width: 100%;
      }
    }
    p {
      font-size: 18px;
      line-height: 120%;
      color: #6C6C6C;
    }
  }
  &__info2 {
    font-size: 20px;
    line-height: 120%;
    color: #EE842C;
    margin-top: 15px;
  }
}

@media screen and (max-width: 1200px) {
  .making {
    &__mob-btn {
      display: flex;
    }
    &__right {
      flex-direction: column;
      .making__form {
        margin-top: 0;
      }
    }
    &__item {
      &-address {
        width: 100%;
        .making__item-top {
          display: none;
        }
      }
      &-map {
        order: -1;
        width: 100%;
        margin: 0 0 30px 0;
        .making__item-top {
          display: flex;
        }
      }
      &-btn {
        display: none;
      }
      &-wrap {
        margin-top: 0;
      }
    }
    #map,
    #map3,
    #map4,
    #map1\.1,
    #map3\.1,
    #map4\.1 {
      height: 270px;
    }
  }
}

@media screen and (max-width: 992px) {
  .making {
    &__link {
      width: auto;
      height: 40px;
      border-radius: 10px;
      padding: 5px 20px;
      font-size: 14px;
      &:not(:first-child) {
        margin-left: 15px;
      }
    }
    &__tabs {
      margin-top: 30px;
      &-head {
        width: calc(100% + 12px);
      }
    }
    &__tab {
      width: 260px;
      margin: 0 12px 0 0 !important;
      &-price {
        margin-top: 25px;
      }
    }
    &__item {
      &-title {
        font-size: 20px;
      }
      &-detailed {
        font-size: 14px;
      }
    }
    &__form {
      gap: 16px;
      margin-top: 20px;
    }
    #map,
    #map2,
    #map3,
    #map4,
    #map1\.1,
    #map2\.1,
    #map3\.1,
    #map4\.1 {
      margin-top: 20px;
    }
    &__checkbox {
      label {
        font-size: 16px;
      }
    }
    &-payment {
      &__title {
        font-size: 20px;
      }
      &__checkbox {
        width: calc(50% - 40px) !important;
        &-icon {
          max-width: 32px;
        }
        &-icon2 {
          max-width: 93px;
        }
      }
      &__btn {
        height: 54px;
      }
    }
  }
  .shipping-methods {
    &__title {
      font-size: 22px;
    }
    &__group {
      display: block;
    }
    &__info {
      margin: 15px 0 0 0;
      p {
        font-size: 16px;
      }
    }
    &__info2 {
      font-size: 16px;
    }
  }
}

@media screen and (max-width: 768px) {
  .making {
    &__mob-btn {
      width: 100%;
      height: 50px;
      margin-bottom: 50px;
    }
    &__items {
      display: block;
    }
    &__left {
      width: 100%;
      margin: 0 0 50px 0;
    }
    &__right {
      width: 100%;
    }
    &-payment {
     &__checkboxes {
       width: 100%;
       display: block;
     }
     &__checkbox {
       width: 100% !important;
       margin: 0;
       &:not(:first-child) {
         margin: 20px 0 0 0;
       }
       &-desc {
         justify-content: start;
       }
     }
     &__info {
       margin-top: 30px;
     }
     &__btn {
       width: 100%;
       margin-top: 15px;
     }
    }
  }
}