.categories {
  &__top {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 20px;
  }
  &__subtitle {
    font-size: 18px;
    line-height: 120%;
    margin-top: 20px;
  }
  &__settings {
    width: 24px;
    cursor: pointer;
    display: none;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__select {
    margin-left: 32px;
    .nice-select {
      display: flex;
      align-items: center;
      width: 222px;
      min-height: 43px;
      height: 100%;
      background: #F0F0F4;
      border-radius: 8px;
      padding: 5px 22px 5px 14px;
      &:after {
        top: 50%;
        right: 10px;
        transform: translate(0, -50%);
        width: 12px;
        height: 8px;
        background-image: url("../img/select-arrow2.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
      .current {
        font-size: 14px;
        line-height: 120%;
        font-weight: 600;
      }
      .list {
        li {
          font-size: 16px;
          border-top: none;
        }
      }
    }
  }
  &__content {
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
  }
  &__sidebar {
    width: 302px;
    margin-right: 24px;
    &-top {
      display: none;
    }
    &-btn {
      display: none;
    }
  }
  &__cards {
    width: calc(100% - 326px);
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
  }
  &__item {
    background: #FFF;
    border: 1px solid #E4E4E4;
    border-radius: 15px;
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      cursor: pointer;
      padding: 16px;
      span {
        font-size: 16px;
        line-height: 120%;
      }
      i {
        flex-shrink: 0;
        width: 12px;
        margin-left: 24px;
        transition: all 0.3s;
        img {
          width: 100%;
        }
      }
      &--active {
        i {
          transform: rotate(-180deg);
        }
      }
    }
    &-hidden {
      padding: 0 16px 16px;
      display: none;
    }
    .checkboxes {
      display: grid;
      grid-template-columns: 100%;
      gap: 10px;
    }
    .checkbox {
      position: relative;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
        opacity: 0;
        cursor: pointer;
        &:checked ~ label {
          &:after {
            border: 1px solid #A31D1C;
          }
          &:before {
            opacity: 1;
            pointer-events: all;
          }
        }
      }
      label {
        display: block;
        font-size: 16px;
        line-height: 120%;
        padding-left: 21px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          transform: translate(0, -50%);
          width: 14px;
          height: 14px;
          border: 1px solid #E4E4E4;
          border-radius: 3px;
          transition: all 0.3s;
        }
        &:before {
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          transform: translate(0, -50%);
          width: 14px;
          height: 14px;
          background: #A31D1C;
          border-radius: 3px;
          opacity: 0;
          pointer-events: none;
          transition: all 0.3s;
        }
      }
    }
  }
  &__grid {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    gap: 24px;
  }
  &__btn {
    width: 290px;
    height: 64px;
    background: #F0F0F4;
    border-radius: 8px;
    margin: 30px auto 0;
    span {
      font-size: 20px;
      line-height: 120%;
    }
    &:hover {
      background: #d6d6de;
    }
  }
  .pagination {
    margin-top: 30px;
  }
}

@media screen and (max-width: 1250px) {
  .categories {
    &__grid {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
      gap: 24px;
    }
  }
}

@media screen and (max-width: 992px) {
  .categories {
    &__top {
      justify-content: space-between;
    }
    &__settings {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &__content {
      margin-top: 30px;
    }
    &__sidebar {
      position: fixed;
      z-index: 10;
      top: 0;
      left: -105%;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: #ffffff;
      padding: 0 15px 35px;
      transition: all 0.5s;
      overflow-y: auto;
      &--active {
        left: 0;
      }
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: transparent;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #B4B4B4;
        border-radius: 3px;
      }
      &-top {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        padding: 16px 0;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          left: -15px;
          bottom: 0;
          width: calc(100% + 15px);
          height: 1px;
          background: #EEE;
        }
      }
      &-title {
        font-size: 20px;
        line-height: 120%;
      }
      &-back {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 12px;
        cursor: pointer;
        svg {
          width: 100%;
          height: auto;
        }
      }
      &-btn {
        display: flex;
        width: 100%;
        margin-top: 30px;
      }
    }
    &__items {
      margin-top: 30px;
    }
    &__cards {
      width: 100%;
    }
    &__grid {
      grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    }
  }
}

@media screen and (max-width: 900px) {
  .categories {
    &__grid {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
    }
  }
}

@media screen and (max-width: 768px) {
  .categories {
    &__grid {
      grid-template-columns: repeat(3, calc(100% / 3 - 24px / 3));
      gap: 12px;
    }
    &__btn {
      width: 231px;
      height: 51px;
      margin-top: 20px;
      span {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .categories {
    &__grid {
      grid-template-columns: repeat(2, calc(50% - 12px / 2));
    }
  }
}

@media screen and (max-width: 360px) {
  .categories {
    &__grid {
      max-width: 174px;
      grid-template-columns: 100%;
      margin: 0 auto;
    }
  }
}