.about {
  &-banner {
    background-image: url("../img/about-banner__img.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 30px;
    padding: 80px 24px;
    position: relative;
    margin-bottom: 50px;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: linear-gradient(90deg, rgba(255, 255, 255, 0.00) 0%, rgba(255, 255, 255, 0.74) 21.85%, rgba(255, 255, 255, 0.86) 50.05%, rgba(255, 255, 255, 0.72) 78.60%, rgba(255, 255, 255, 0.00) 100%);
    }
    &__content {
      position: relative;
      z-index: 2;
    }
    &__title {
      display: flex;
      justify-content: center;
      align-items: center;
      span {
        font-family: Unbounded;
        font-size: 40px;
        line-height: 120%;
        font-weight: 500;
      }
      img {
        max-width: 565px;
        width: 100%;
        margin-left: 16px;
      }
    }
    &__subtitle {
      font-family: Unbounded;
      font-weight: 500;
      margin-top: 25px;
      text-align: center;
      span {
        color: #A31D1C;
      }
    }
  }
  &__text {
    text-align: center;
  }
  &__subtitle {
    text-align: center;
    margin-top: 30px;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 100px / 3));
    gap: 50px;
    margin: 50px 0;
  }
  &__item {
    max-width: 325px;
    margin: 0 auto;
    text-align: center;
    &-img {
      width: 200px;
      margin: 0 auto;
      img {
        width: 100%;
      }
    }
    &-title {
      font-size: 26px;
      line-height: 130%;
      font-weight: 500;
      margin-top: 20px;
    }
  }
  &__bottom {
    display: flex;
    justify-content: space-between;
    background: #F2F2F2;
    border-radius: 30px;
    padding: 30px 40px;
    margin-top: 50px;
    &-desc {
      width: calc(100% - 490px);
    }
    &-img {
      width: 470px;
      margin-left: 20px;
      img {
        width: 100%;
        border-radius: 20px;
      }
    }
  }
}
.about-feedback {
  padding: 50px 0 0 0;
  &__inner {
    display: flex;
  }
  &__desc {
    width: calc(100% - 655px);
    background: #F2F2F2;
    border-radius: 30px;
    padding: 35px;
  }
  &__form {
    width: 620px;
    margin-left: 35px;
    display: flex;
    flex-direction: column;
  }
  &__img {
    margin-bottom: 32px;
    img {
      width: 100%;
      height: 100%;
      min-height: 320px;
      border-radius: 20px;
    }
  }
  &__title {
    font-size: 35px;
    line-height: 120%;
    color: #A31D1C;
    letter-spacing: 1.4px;
    margin-bottom: 6px;
  }
  .about-form {
    flex-grow: 1;
    background: #F0F0F4;
    border-radius: 30px;
    padding: 50px 33px;
    margin-top: 38px;
    &__group {
      margin: 0;
      padding: 0;
      border: none;
      &:not(:first-child) {
        margin-top: 17px;
      }
      input {
        width: 368px;
        height: 67px;
        background: #FFF;
        border-color: transparent;
        border-radius: 10px;
        padding: 5px 20px;
        font-size: 16px;
        line-height: 120%;
        &::placeholder {
          color: #75767C;
        }
      }
      textarea {
        width: 100%;
        height: 204px;
        background: #FFF;
        border-color: transparent;
        border-radius: 10px;
        padding: 20px;
        font-size: 16px;
        line-height: 120%;
        resize: none;
        &::placeholder {
          color: #75767C;
        }
      }
    }
    &__btn {
      width: 340px;
      margin-top: 30px;
    }
    &__info {
      font-size: 14px;
      line-height: 140%;
      color: #75767C;
      margin-top: 17px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .about-feedback {
    &__desc {
      width: calc(100% - 530px);
    }
    &__form {
      width: 500px;
      margin-left: 30px;
    }
  }
}

@media screen and (max-width: 1150px) {
  .about {
    &__bottom {
      padding: 30px 24px;
      &-desc {
        width: calc(100% - 420px);
      }
      &-img {
        width: 400px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .about {
    &-banner {
      &__title {
        span {
          font-size: 30px;
        }
        img {
          max-width: 300px;
        }
      }
      &__subtitle {
        margin-top: 15px;
      }
    }
    &__items {
      grid-template-columns: repeat(2, calc(50% - 30px / 2));
      gap: 30px;
    }
    &__item {
      &-img {
        width: 120px;
      }
      &-title {
        font-size: 20px;
        margin-top: 16px;
      }
    }
    &__bottom {
      flex-direction: column;
      padding: 24px;
      &-desc {
        width: 100%;
        text-align: center;
      }
      &-img {
        order: -1;
        width: 100%;
        max-width: 500px;
        margin: 0 auto 30px;
      }
    }
  }
  .about-feedback {
    &__desc {
      display: none;
    }
    &__form {
      width: 100%;
      margin: 0;
    }
    &__title {
      font-size: 28px;
    }
  }
}

@media screen and (max-width: 576px) {
  .about {
    &-banner {
      padding: 40px 16px;
      margin-bottom: 20px;
      &__title {
        span {
          font-size: 16px;
        }
        img {
          max-width: 220px;
          margin-left: 8px;
        }
      }
      &__subtitle {
        font-size: 14px;
      }
    }
    &__items {
      grid-template-columns: 100%;
    }
  }
  .about-feedback {
    .about-form {
      padding: 24px;
      margin-top: 30px;
      &__group {
        input {
          width: 100%;
          height: 52px;
          padding: 5px 14px;
        }
        textarea {
          height: 160px;
          padding: 17px 14px;
        }
      }
      &__btn {
        width: 100%;
        margin-top: 25px;
      }
    }
    &__form-top {
      text-align: center;
    }
  }
}

@media screen and (max-width: 360px) {
  .about {
    &-banner {
      &__title {
        img {
          max-width: 180px;
        }
      }
    }
  }
}