.basket-empty {
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__img {
    width: 194px;
    margin: 0 auto;
    img {
      width: 100%;
    }
  }
  &__title {
    margin-top: 50px;
  }
  &__text {
    max-width: 420px;
    font-size: 20px;
    line-height: 140%;
    color: #949494;
    margin-top: 22px;
  }
  &__catalog {
    width: 157px;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #A31D1C;
    border-radius: 5px;
    margin-top: 35px;
    span {
      font-size: 18px;
      line-height: 120%;
      color: #ffffff;
    }
    i {
      flex-shrink: 0;
      width: 21px;
      margin-left: 12px;
      img {
        width: 100%;
      }
    }
  }
}
.basket {
  &__top {
    display: flex;
    align-items: center;
  }
  &__info {
    display: flex;
    align-items: center;
    margin-left: 40px;
  }
  &__stock {
    display: flex;
    align-items: center;
    &-icon {
      flex-shrink: 0;
      width: 24px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 15px;
    }
    &-text {
      font-size: 20px;
      line-height: 120%;
      color: #64AD01;
    }
  }
  &__order {
    display: flex;
    align-items: center;
    margin-left: 40px;
    &-icon {
      flex-shrink: 0;
      width: 24px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 15px;
    }
    &-text {
      font-size: 20px;
      line-height: 120%;
      color: #EE842C;
    }
  }
  &__content {
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
  }
  &__left {
    width: 770px;
    margin-right: 20px;
    .basket__group {
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
  }
  &__right {
    width: calc(100% - 790px);
    .basket__group {
      &:not(:first-child) {
        margin-top: 20px;
      }
    }
  }
  &__subtitle {
    font-size: 25px;
    line-height: 120%;
    border-bottom: 1px solid #EBEBEB;
    padding-bottom: 5px;
  }
  &__card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 25px;
    &-left {
      width: 58%;
      display: flex;
      align-items: center;
      .dropdown-order-item__close {
        display: none !important;
      }
    }
    &-right {
      width: 42%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-left: 25px;
    }
    &-img {
      flex-shrink: 0;
      width: 68px;
      margin-right: 12px;
      img {
        width: 100%;
      }
    }
    &-text {
      max-width: 210px;
      p:nth-child(1) {
        font-size: 16px;
        line-height: 120%;
      }
      p:nth-child(2) {
        font-size: 14px;
        line-height: 120%;
        color: #6C6C6C;
        letter-spacing: 0.84px;
        margin-top: 5px;
      }
    }
    &-info {
      margin-left: 25px;
      p:nth-child(1) {
        font-size: 14px;
        line-height: 120%;
      }
      p:nth-child(2) {
        font-size: 14px;
        line-height: 120%;
        letter-spacing: 0.84px;
        margin-top: 28px;
      }
    }
    .green-text {
      color: #64AD01;
    }
    .orange-text {
      color: #EE842C;
    }
  }
  .plus-minus {
    display: flex;
    align-items: center;
    button {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 28px;
      height: 28px;
      background: #ffffff;
      border: 1px solid #A31D1C;
      border-radius: 50%;
      padding: 0;
      margin: 0;
      font-size: 25px;
      line-height: 105%;
      font-weight: 300;
      color: #A31D1C;
      &:first-of-type {
        opacity: 1;
        pointer-events: all;
      }
      &:hover {
        background: #A31D1C;
        color: #ffffff;
      }
    }
    input {
      opacity: 1;
      pointer-events: all;
      width: 40px;
      border: none;
      text-align: center;
      font-size: 14px;
      line-height: 105%;
      font-weight: 500;
      margin: 0 5px;
    }
  }
  .prices {
    flex-shrink: 0;
    display: flex;
    align-items: center;
  }
  .old-price {
    margin-left: 25px;
    span:nth-child(1) {
      font-size: 14px;
      line-height: 120%;
      color: #75767C;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(15deg);
        width: 100%;
        height: 1px;
        background: #A31D1C;
      }
    }
    span:nth-child(2) {
      display: flex;
      justify-content: center;
      align-items: center;
      background: #A31D1C;
      border-radius: 5px;
      color: #ffffff;
      font-size: 12px;
      line-height: 120%;
      padding: 3px;
      margin-top: 5px;
    }
  }
  .new-price {
    margin-left: 25px;
    span {
      font-size: 18px;
      line-height: 120%;
    }
  }
  .dropdown-order-item__close {
    margin-left: 25px;
  }
  &__btns {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
  &__btns2 {
    margin-top: 20px;
  }
  &__btn {
    display: flex;
    align-items: center;
    margin-bottom: 16px;
    cursor: pointer;
    &:not(:last-child) {
      margin-right: 22px;
    }
    i {
      flex-shrink: 0;
      width: 28px;
      height: 28px;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid #75767C;
      border-radius: 50%;
      margin-right: 10px;
    }
    span {
      font-size: 18px;
      line-height: 120%;
      color: #75767C;
    }
    &:hover {
      i {
       border-color: #A31D1C;
        svg path {
          fill: #A31D1C;
          stroke: #A31D1C;
        }
        svg g {
          fill: #A31D1C;
          stroke: #A31D1C;
        }
      }
      span {
        color: #A31D1C;
      }
    }
  }
  &__btn2 {
    i {
      border: 1px solid #A31D1C;
    }
    span {
      color: #A31D1C;
    }
  }
  &__wrap {
    background: #F9F9F9;
    border-radius: 10px;
    padding: 50px 24px;
    .red-btn {
      width: 100%;
    }
  }
  &__form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 14px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.08);
    &-text {
      font-size: 20px;
      line-height: 120%;
      color: #949494;
      margin-right: 24px;
      margin-bottom: 16px;
    }
    &-group {
      position: relative;
      flex-grow: 1;
      margin-bottom: 16px;
      input {
        width: 100%;
        height: 51px;
        background: #FFF;
        border: 0.5px solid #A31D1C;
        border-radius: 10px;
        padding: 5px 55px 5px 15px;
        font-size: 16px;
        line-height: 120%;
        color: #949494;
      }
    }
    &-btn {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 51px;
      height: 51px;
      background: #A31D1C;
      border-radius: 10px;
    }
  }
  &__payment {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 30px;
    margin-bottom: 14px;
    &-desc {
      margin-right: 24px;
      margin-bottom: 16px;
      p:nth-child(1) {
        font-size: 25px;
        line-height: 120%;
      }
      p:nth-child(2) {
        font-size: 16px;
        line-height: 120%;
        color: #6C6C6C;
        letter-spacing: 0.96px;
        margin-top: 5px;
      }
    }
    &-info {
      max-width: 325px;
      font-size: 16px;
      line-height: 120%;
      margin-right: 24px;
      margin-bottom: 16px;
    }
  }
  &__payment2 {
    margin-top: 8px;
  }
  &__price {
    font-size: 30px;
    line-height: 105%;
    margin-bottom: 16px;
  }
  &__ranges {
    margin-bottom: 30px;
  }
  &__range {
    border-bottom: 1px solid rgba(0, 0, 0, 0.07);
    &:not(:last-child) {
      margin-bottom: 20px;
      padding-bottom: 20px;
    }
    &-title {
      display: flex;
      font-size: 20px;
      line-height: 120%;
      letter-spacing: 1.2px;
      img {
        flex-shrink: 0;
        width: 29px;
        margin-right: 10px;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      color: #6C6C6C;
      letter-spacing: 0.96px;
      margin-top: 7px;
    }
    &-group {
      margin-top: 22px;
      .range-price {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        font-size: 16px;
        line-height: 120%;
        margin-top: 15px;
        span:not(:first-child) {
          margin-left: 3px;
        }
      }
    }
    &-output {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      font-size: 16px;
      line-height: 120%;
      margin-top: 10px;
      span {
        margin-right: 3px;
      }
    }
  }
  .disabled {
    opacity: 0.3;
  }
  .noUi-target {
    background: transparent;
    border-color: transparent;
    box-shadow: unset;
    border-radius: 10px;
  }
  .noUi-horizontal {
    height: 6px;
    .noUi-handle {
      top: -15px;
      width: 36px;
      height: 36px;
      background: transparent;
      border-color: transparent;
      box-shadow: unset;
      &:after {
        display: none;
      }
      &:before {
        display: none;
      }
    }
    .noUi-touch-area {
      background-image: url("../img/range-icon.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }
  }
  .noUi-connects {
    height: 6px;
    background: #EBEBEB;
    border-radius: 10px;
  }
  .noUi-connect {
    background-image: linear-gradient(to left, #d8d6d6, #9d9c9c, #666565, #343333, #000000);
  }
  ._range-block2 {
    .noUi-touch-area {
      background-image: url("../img/range-icon2.svg");
    }
  }
}
.order-basket {
  padding-bottom: 25px;
  &__title {
    font-size: 40px;
    line-height: 120%;
  }
  &__subtitle {
    margin-top: 15px;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    margin-top: 40px;
  }
  &__left {
    width: 510px;
    border: 1px solid #CACACA;
    border-radius: 20px;
    padding: 40px;
    margin-right: 40px;
    .order-basket__item {
      &:not(:first-child) {
        margin-top: 70px;
      }
    }
  }
  &__right {
    width: calc(100% - 550px);
    border: 1px solid #CACACA;
    border-radius: 20px;
    padding: 40px;
    .order-basket__item {
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
  }
  &__item {
    display: flex;
    &-icon {
      width: 27px;
      flex-shrink: 0;
      display: flex;
      align-items: start;
      margin-right: 12px;
      img {
        width: 100%;
      }
    }
    &-title {
      font-size: 22px;
      line-height: 120%;
      font-weight: 500;
      color: #393939;
    }
    &-text {
      display: flex;
      align-items: center;
      font-size: 22px;
      line-height: 130%;
      color: #393939;
      margin-top: 6px;
      i {
        flex-shrink: 0;
        max-width: 18px;
        display: flex;
        align-items: center;
        margin-right: 8px;
        img {
          width: 100%;
        }
      }
      span {
        line-height: 120%;
      }
    }
    &-payment {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      background: #F9F9F9;
      border-radius: 4px;
      padding: 12px 12px 0 12px;
      margin-top: 10px;
      span:nth-child(1) {
        font-size: 18px;
        line-height: 120%;
        color: #393939;
        margin-right: 24px;
        margin-bottom: 10px;
      }
      span:nth-child(2) {
        font-size: 18px;
        line-height: 120%;
        margin-bottom: 10px;
      }
    }
    &-images {
      width: calc(100% + 12px);
      display: flex;
      flex-wrap: wrap;
      align-items: stretch;
      margin-top: 10px;
    }
    &-img {
      width: 67px;
      margin: 0 12px 12px 0;
      img {
        width: 100%;
      }
    }
    &-info {
      display: flex;
      justify-content: space-between;
      background: #F9F9F9;
      border-radius: 4px;
      padding: 12px 15px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .basket {
    &__left {
      width: 600px;
    }
    &__right {
      width: calc(100% - 620px);
    }
    &__card {
      display: block;
      &-left {
        width: 100%;
        padding-right: 40px;
        position: relative;
        .dropdown-order-item__close {
          display: flex !important;
          position: absolute;
          top: 50%;
          right: 0;
          transform: translate(0, -50%);
        }
      }
      &-right {
        width: 100%;
        margin: 22px 0 0 0;
      }
      &-info {
        display: none;
      }
      .prices {
        order: -3;
      }
      .old-price {
        order: -3;
        margin: 0 25px 0 0;
      }
      .new-price {
        order: -2;
        margin: 0 25px 0 0;
      }
      .dropdown-order-item__close {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .basket {
    &__left {
      width: calc(100% - 420px);
      margin: 0;
    }
    &__right {
      width: 400px;
      margin-left: 20px;
    }
  }
  .order-basket {
    &__left {
      width: 50%;
      margin-right: 30px;
      padding: 30px 24px;
    }
    &__right {
      width: 50%;
      padding: 30px 24px;
    }
  }
}

@media screen and (max-width: 992px) {
  .basket-empty {
    &__img {
      width: 160px;
    }
    &__title {
      margin-top: 30px;
    }
    &__text {
      font-size: 16px;
      margin-top: 12px;
    }
    &__catalog {
      margin-top: 20px;
      span {
        font-size: 16px;
      }
    }
  }
  .basket {
    &__top {
      display: block;
    }
    &__info {
      margin: 20px 0 0 0;
    }
    &__stock {
      &-text {
        font-size: 16px;
      }
    }
    &__order {
      margin-left: 20px;
      &-icon {
        width: 19px;
        margin-right: 5px;
      }
      &-text {
        font-size: 16px;
      }
    }
    &__content {
      display: block;
      margin-top: 16px;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      margin: 15px 0 0 0;
    }
    &__subtitle {
      font-size: 18px;
    }
    &__card {
      margin-top: 12px;
      &-text {
        p:nth-child(1) {
          font-size: 14px;
        }
        p:nth-child(2) {
          font-size: 12px;
        }
      }
    }
    &__btns {
      display: none;
    }
    &__btns2 {
      display: flex;
    }
    &__btn {
      span {
        font-size: 16px;
      }
    }
    &__wrap {
      padding: 30px 16px;
    }
    &__form {
      padding-bottom: 4px;
      &-text {
        font-size: 16px;
      }
      &-group {
        input {
          height: 44px;
          font-size: 14px;
        }
      }
      &-btn {
        width: 44px;
        height: 44px;
      }
    }
    &__payment {
      margin-bottom: 4px;
      &-desc {
        p:nth-child(1) {
          font-size: 20px;
        }
        p:nth-child(2) {
          font-size: 14px;
          margin-top: 12px;
        }
      }
      &-info {
        font-size: 14px;
        margin-top: 12px;
      }
    }
    &__price {
      font-size: 24px;
    }
    &__range {
      &-title {
        font-size: 18px;
      }
      &-text {
        font-size: 14px;
      }
    }
  }
  .order-basket {
    &__title {
      font-size: 26px;
    }
    &__subtitle {
      font-size: 18px;
    }
    &__content {
      margin-top: 30px;
    }
    &__left {
      padding: 30px 12px;
    }
    &__rifgt {
      padding: 30px 12px;
    }
    &__item {
      &-icon {
        width: 25px;
      }
      &-title {
        font-size: 18px;
      }
      &-text {
        font-size: 18px;
      }
      &-img {
        width: 45px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .order-basket {
    &__content {
      display: block;
    }
    &__left {
      width: 100%;
      margin: 0 0 30px 0;
      .order-basket__item {
        &:not(:first-child) {
          margin-top: 30px;
        }
      }
    }
    &__right {
      width: 100%;
    }
  }
}

@media screen and (max-width: 360px) {
  .order-basket {
    &__item {
      &-icon {
        width: 20px;
      }
      &-title {
        font-size: 16px;
      }
      &-text {
        font-size: 14px;
      }
      &-info {
        font-size: 16px;
      }
      &-payment {
        span:nth-child(1) {
          font-size: 14px;
        }
        span:nth-child(2) {
          font-size: 14px;
        }
      }
    }
  }
}