.delivery {
  padding: 0 0 30px 0;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__links {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    margin-left: 15px;
  }
  &__link {
    display: flex;
    align-items: center;
    font-size: 18px;
    line-height: 120%;
    color: #585858;
    border-bottom: 1px dashed #9C9C9C;
    i {
      margin-right: 5px;
      svg path {
        stroke: #585858;
      }
    }
    &:not(:first-child) {
      margin-left: 59px;
    }
    &:hover {
      color: #A31D1C;
      i svg {
        stroke: #A31D1C;
      }
    }
    &--active {
      font-weight: 500;
      color: #A31D1C;
      border: none;
      i svg path {
        stroke: #A31D1C;
      }
    }
  }
  &__slider {
    position: relative;
    margin-top: 45px;
  }
  &__swiper-wrapper {
    align-items: stretch;
  }
  &-card {
    position: relative;
    height: auto;
    &__input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      padding: 0;
      margin: 0;
      opacity: 0;
      cursor: pointer;
      &:checked ~ .delivery-card__desc {
        background: #FCE8DB;
      }
    }
    &__desc {
      height: 100%;
      background: #F9F9F9;
      border-radius: 25px;
      padding: 25px 12px;
    }
    &__title {
      font-size: 20px;
      line-height: 120%;
      font-weight: 500;
    }
    &__items {
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
      margin-top: 15px;
    }
    &__item {
      display: flex;
      align-items: center;
    }
    &__icon {
      flex-shrink: 0;
      width: 25px;
      margin-right: 15px;
      img {
        width: 100%;
      }
    }
    &__text {
      font-size: 16px;
      line-height: 140%;
      color: #6C6C6C;
      span {
        font-size: 18px;
        line-height: 140%;
        color: #000000;
      }
    }
  }
  .swiper-pagination {
    top: unset !important;
    bottom: -12px !important;
    height: 3px;
    background: transparent;
    border-radius: 5px;
    &-progressbar-fill {
      background: #B4B4B4 !important;
      border-radius: 5px;
    }
  }
}
.payment {
  padding: 30px 0 50px 0;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__links {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    margin-left: 15px;
  }
  &__link {
    max-width: 100px;
    img {
      max-width: 100%;
    }
    &:not(:first-child) {
      margin-left: 47px;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 36px / 4));
    gap: 12px;
    margin-top: 40px;
  }
  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F9F9F9;
    border-radius: 10px;
    padding: 20px;
    &-icon {
      flex-shrink: 0;
      max-width: 43px;
      margin-right: 15px;
      img {
        width: 100%;
      }
    }
    &-desc {
      font-size: 16px;
      line-height: 140%;
      font-weight: 500;
    }
  }
}
.shipping-cost {
  padding: 50px 0 25px;
  &__content {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
  }
  &__info {
    width: 220px;
    margin-right: 25px;
  }
  &__table {
    width: calc(100% - 245px);
  }
  &__item {
    &:not(:first-child) {
      margin-top: 60px;
    }
  }
  &__icon {
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #A31D1C;
    border-radius: 50%;
    img {
      max-width: 32px;
    }
  }
  &__text {
    font-size: 20px;
    line-height: 140%;
    color: #6C6C6C;
    margin-top: 20px;
  }
  table {
    width: 100%;
    border-spacing: 10px;
    thead {
      tr {
        th {
          height: 72px;
          background: #FBE8DB;
          border-radius: 10px;
          font-size: 25px;
          line-height: 120%;
          font-weight: 600;
          &:nth-child(1) {
            width: 120px;
            background: rgba(163, 29, 28, 0.16);
            padding: 5px;
            font-size: 15px;
            font-weight: 500;
          }
          &:nth-child(2) {
            width: 120px;
            background: #FBE8DB;
            padding: 5px;
            font-size: 15px;
            font-weight: 500;
          }
          &:nth-child(3) {
            width: 120px;
            background: #F9F9F9;
            padding: 5px;
            font-size: 15px;
            font-weight: 500;
          }
        }
      }
    }
    tbody {
      tr {
        td {
          height: 96px;
          text-align: center;
          background: #F9F9F9;
          border-radius: 10px;
          font-size: 25px;
          line-height: 120%;
          letter-spacing: 0.55px;
          padding: 5px 20px;
          &:nth-child(1) {
            text-align: start;
            background: rgba(163, 29, 28, 0.16);
            font-size: 25px;
          }
        }
      }
    }
  }
}
.refund {
  padding: 25px 0;
  &__content {
    background: #F9F9F9;
    border-radius: 30px;
    padding: 50px 35px;
    text-align: center;
    margin-top: 30px;
  }
  &__subtitle {
    font-size: 25px;
    line-height: 140%;
  }
  &__text {
    font-size: 18px;
    line-height: 140%;
    color: #6C6C6C;
    margin-top: 14px;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
    gap: 30px;
    margin-top: 50px;
  }
  &__item {
    max-width: 290px;
    margin: 0 auto;
    &-icon {
      max-width: 100px;
      margin: 0 auto;
      position: relative;
      img {
        max-width: 100%;
        position: relative;
        z-index: 1;
      }
      &:after {
        content: '';
        position: absolute;
        top: 15px;
        left: 0;
        width: 62px;
        height: 62px;
        background: #FEDF4A;
        border-radius: 50%;
      }
    }
    &-text {
      font-size: 20px;
      line-height: 120%;
      margin-top: 15px;
    }
    &-info {
      font-size: 18px;
      line-height: 120%;
      color: #6C6C6C;
      margin-top: 10px;
    }
  }
  &__btn {
    width: 340px;
    margin: 80px auto 0;
  }
}
.map {
  padding: 25px 0;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__search {
    flex-grow: 1;
    max-width: 962px;
    position: relative;
    margin-left: 32px;
    input {
      width: 100%;
      height: 44px;
      background: #F0F0F4;
      border-color: transparent;
      border-radius: 8px;
      padding: 5px 15px 5px 40px;
      font-size: 14px;
      line-height: 120%;
      color: #75767C;
    }
    img {
      position: absolute;
      top: 50%;
      left: 15px;
      transform: translate(0, -50%);
    }
  }
  &__content {
    margin-top: 25px;
    position: relative;
  }
  #map {
    width: 100%;
    height: 675px;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
    }
    .ymaps-2-1-79-inner-panes {
      border-radius: 20px;
    }
  }
  &__desc {
    max-width: 470px;
    background: #ffffff;
    box-shadow: 1px 0px 10px 0px rgba(112, 112, 112, 0.15);
    border-radius: 20px;
    padding: 32px;
    position: absolute;
    top: 50%;
    right: -200px;
    transform: translate(0, -50%);
  }
  &__subtitle {
    font-size: 25px;
    line-height: 120%;
  }
  &__text {
    font-size: 22px;
    line-height: 120%;
    margin-top: 20px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 15px;
  }
  &__item {
    display: flex;
    align-items: center;
    &-icon {
      flex-shrink: 0;
      max-width: 27px;
      margin-right: 15px;
      img {
        max-width: 100%;
      }
    }
    &-desc {
      max-width: 305px;
      font-size: 18px;
      line-height: 120%;
      color: #696969;
    }
    &-copy {
      display: flex;
      align-items: center;
      padding-left: 42px;
      span {
        font-size: 16px;
        line-height: 120%;
        color: #64AD01;
      }
      i {
        flex-shrink: 0;
        width: 15px;
        margin-left: 15px;
      }
    }
  }
}
.delivery-info {
  padding: 25px 0 0 0;
  &__inner {
    text-align: center;
    background: #F9F9F9;
    border-radius: 30px;
    padding: 40px 32px 70px 32px;
  }
  &__title {
    color: #6C6C6C;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 188px / 3));
    gap: 45px 90px;
    margin-top: 40px;
  }
  &__item {
    &-icon {
      width: 50px;
      margin: 0 auto;
      img {
        width: 100%;
      }
    }
    &-desc {
      font-size: 20px;
      line-height: 140%;
      color: #6F6F6F;
      margin-top: 24px;
    }
  }
}


@media screen and (max-width: 1800px) {
  .map {
    &__desc {
      right: 15px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .delivery {
    &__top {
      display: block;
    }
    &__links {
      margin: 16px 0 0 0;
      overflow-x: auto;
      padding-bottom: 10px;
      &::-webkit-scrollbar {
        height: 3px;
      }
      &::-webkit-scrollbar-track {
        background: transparent;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #B4B4B4;
        border-radius: 3px;
      }
    }
    &__link {
      flex-shrink: 0;
    }
  }
  .payment {
    &__top {
      display: block;
    }
    &__links {
      margin: 16px 0 0 0;
      overflow-x: auto;
      padding-bottom: 10px;
      &::-webkit-scrollbar {
        height: 3px;
      }
      &::-webkit-scrollbar-track {
        background: transparent;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #B4B4B4;
        border-radius: 3px;
      }
    }
    &__link {
      flex-shrink: 0;
    }
    &__items {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
      gap: 20px;
    }
  }
  .shipping-cost {
    .container {
      padding: 0;
    }
    &__title {
      padding: 0 15px;
    }
    &__content {
      display: block;
    }
    &__info {
      width: 100%;
      display: flex;
      justify-content: space-between;
      padding: 0 15px;
      margin: 0 0 50px 0;
    }
    &__table {
      width: 100%;
      padding-bottom: 10px;
      overflow-x: auto;
      &::-webkit-scrollbar {
        height: 3px;
      }
      &::-webkit-scrollbar-track {
        background: transparent;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #B4B4B4;
        border-radius: 3px;
      }
    }
    table {
      width: 1160px;
      padding: 0 15px;
    }
    &__item {
      display: flex;
      align-items: center;
      &:not(:first-child) {
        margin: 0 0 0 20px;
      }
    }
    &__icon {
      flex-shrink: 0;
    }
    &__text {
      margin: 0 0 0 16px;
    }
  }
}

@media screen and (max-width: 992px) {
  .delivery {
    &-card {
      &__title {
        font-size: 18px;
      }
      &__text {
        font-size: 14px;
        span {
          font-size: 16px;
        }
      }
    }
  }
  .payment {
    padding: 30px 0;
  }
  .shipping-cost {
    &__info {
      overflow-x: auto;
      padding-bottom: 10px;
      &::-webkit-scrollbar {
        height: 3px;
      }
      &::-webkit-scrollbar-track {
        background: transparent;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #B4B4B4;
        border-radius: 3px;
      }
    }
    &__item {
      flex-shrink: 0;
    }
    &__icon {
      width: 32px;
      height: 32px;
      img {
        max-width: 20px;
      }
    }
    &__text {
      font-size: 16px;
      max-width: 220px;
    }
    table {
      width: 992px;
      thead {
        tr {
          th {
            font-size: 16px !important;
          }
        }
      }
      tbody {
        tr {
          td {
            height: 72px;
            font-size: 16px !important;
          }
        }
      }
    }
  }
  .refund {
    &__content {
      padding: 30px 16px;
    }
    &__subtitle {
      font-size: 22px;
    }
    &__text {
      font-size: 16px;
    }
    &__items {
      grid-template-columns: 100%;
      gap: 50px;
    }
    &__item {
      &-icon {
        max-width: 58px;
        &:after {
          top: 5px;
          left: -10px;
          width: 37px;
          height: 37px;
        }
      }
      &-text {
        font-size: 18px;
      }
      &-info {
        font-size: 16px;
      }
    }
    &__btn {
      margin-top: 50px;
    }
  }
  .map {
    &__desc {
      position: static;
      transform: translate(0);
      max-width: 100%;
      background: #F2F2F2;
      margin-top: 20px;
    }
    &__subtitle {
      font-size: 20px;
    }
    &__text {
      font-size: 18px;
    }
    &__item {
      &-icon {
        max-width: 22px;
      }
      &-desc {
        font-size: 16px;
      }
      &-copy {
        padding-left: 0;
      }
    }
  }
  .delivery-info {
    &__items {
      grid-template-columns: 100%;
      gap: 30px;
    }
    &__item {
      max-width: 290px;
      margin: 0 auto;
      &-desc {
        font-size: 18px;
        margin-top: 16px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .payment {
    &__items {
      grid-template-columns: 100%;
    }
  }
  .map {
    &__top {
      display: block;
    }
    &__search {
      margin: 30px 0 0 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .delivery {
    &__link {
      font-size: 16px;
      &:not(:first-child) {
        margin-left: 20px;
      }
    }
    &__slider {
      width: calc(100% + 15px);
    }
  }
  .payment {
    &__link {
      max-width: 90px;
      &:not(:first-child) {
        margin-left: 20px;
      }
    }
    &__item {
      &-icon {
        max-width: 32px;
      }
    }
  }
  .refund {
    &__btn {
      width: 100%;
    }
  }
  .map {
    #map {
      height: 500px;
    }
    &__desc {
      padding: 32px 24px;
    }
  }
  .delivery-info {
    &__inner {
      padding: 32px 24px;
    }
  }
}

@media screen and (max-width: 370px) {
  .delivery {
    &__slider {
      width: 100%;
    }
  }
}

