.product {
  padding: 0 0 25px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: 620px;
    margin-right: 100px;
  }
  &__right {
    width: calc(100% - 720px);
  }
  &__slider {
    position: relative;
    &-btns {
      display: flex;
      align-items: center;
      position: absolute;
      right: 25px;
      bottom: 16px;
    }
  }
  &-card {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #EEEEEE;
    border-radius: 20px;
    position: relative;
    &__img {
      max-width: 400px;
      img {
        width: 100%;
      }
    }
    &__btns {
      position: absolute;
      top: 25px;
      right: 25px;
    }
    &__btn {
      width: 59px;
      height: 59px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #ffffff;
      border-radius: 50%;
      svg {
        max-width: 28px;
      }
      &:not(:first-child) {
        margin-top: 10px;
      }
      &:hover {
        svg path {
          fill: #A31D1C;
          stroke: #A31D1C;
        }
      }
    }
    &__art {
      position: absolute;
      right: 25px;
      bottom: 67px;
      font-size: 20px;
      line-height: 120%;
      color: #6C6C6C;
      letter-spacing: 1.2px;
    }
  }
  .swiper-button-prev {
    position: static;
    width: 35px;
    height: 35px;
    background: #ffffff;
    border-radius: 50%;
    margin-top: 0;
  }
  .swiper-button-next {
    position: static;
    width: 35px;
    height: 35px;
    background: #ffffff;
    border-radius: 50%;
    margin-top: 0;
    margin-left: 14px;
  }
  .swiper-pagination {
    &-bullet {
      background: #ffffff;
      opacity: 1;
      &-active {
        background: #A31D1C;
      }
    }
  }
  &__title {
    font-size: 40px;
    line-height: 120%;
  }
  &__info {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 12px;
  }
  .stars {
    margin: 0 24px 10px 0;
    &__rate {
      color: #D9D9D9;
    }
  }
  .weight {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    &-item {
      display: flex;
      align-items: center;
      i {
        width: 16px;
        margin-right: 11px;
        svg {
          width: 100%;
          height: auto;
        }
      }
      span {
        font-size: 18px;
        line-height: 120%;
        color: #75767C;
      }
    }
    &-info {
      position: relative;
      margin-left: 4px;
      i {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 14px;
        cursor: pointer;
        svg {
          width: 100%;
          height: auto;
        }
      }
     .hover-hide {
       position: absolute;
       left: 100%;
       bottom: 100%;
       width: 226px;
       background: #75767C;
       box-shadow: 1px 0px 10px 0px rgba(112, 112, 112, 0.15);
       border-radius: 4px;
       font-size: 16px;
       line-height: 120%;
       color: #FFF;
       padding: 10px 16px;
       opacity: 0;
       pointer-events: none;
       transition: all 0.3s;
       &--active {
         opacity: 1;
         pointer-events: all;
       }
     }
    }
  }
  &__price {
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;
    &-item {
      margin-bottom: 30px;
      &:not(:last-child) {
        margin-right: 50px;
      }
    }
    &-number {
      font-size: 30px;
      line-height: 120%;
      color: #64AD01;
      letter-spacing: 2.1px;
    }
    &-text {
      font-size: 22px;
      line-height: 120%;
      color: #75767C;
      margin-top: 6px;
    }
  }
  &__add {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    .product__price-number {
      margin: 0 24px 10px 0;
    }
  }
  .plus-minus {
    margin-bottom: 10px;
    button {
      width: 77px;
      height: 77px;
      border-radius: 70px;
      font-size: 60px;
      line-height: 100%;
      font-weight: 300;
      color: #A31D1C;
      cursor: pointer;
      &:hover {
        background: #A31D1C;
        color: #ffffff;
      }
    }
    input {
      width: 120px;
      border: none;
      text-align: center;
      font-size: 25px;
      line-height: 105%;
      font-weight: 500;
      margin: 0 15px;
    }
  }
  &__btn {
    width: 100%;
    margin-top: 30px;
  }
  &__info2 {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 30px;
    &-item {
      display: flex;
      align-items: center;
      margin-bottom: 12px;
      &:not(:last-child) {
        margin-right: 20px;
      }
    }
    &-icon {
      flex-shrink: 0;
      max-width: 21px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 8px;
      svg {
        max-width: 100%;
      }
    }
    &-text {
      font-size: 20px;
      line-height: 120%;
      color: #75767C;
    }
  }
  .tabs {
    margin-top: 65px;
    &__head {
      display: flex;
      align-items: center;
      overflow-x: auto;
      padding-bottom: 10px;
      &::-webkit-scrollbar {
        height: 3px;
      }
      &::-webkit-scrollbar-track {
        background: transparent;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #B4B4B4;
        border-radius: 3px;
      }
    }
    &__body {
      margin-top: 35px;
    }
    .tab {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      width: 248px;
      height: 80px;
      border: 1px solid #F0F0F4;
      box-shadow: 1px 0px 10px 0px rgba(112, 112, 112, 0.15);
      border-radius: 8px;
      cursor: pointer;
      &:not(:last-child) {
        margin-right: 10px;
      }
      &--active {
        background: #F0F0F4;
        box-shadow: unset;
      }
      i {
        max-width: 24px;
        margin-right: 10px;
        img {
          max-width: 100%;
        }
      }
    }
  }
  .about-product {
    display: flex;
    justify-content: space-between;
    &__left {
      width: 510px;
      margin-right: 40px;
    }
    &__right {
      width: calc(100% - 550px);
    }
    &__list {
      display: grid;
      grid-template-columns: 100%;
      gap: 20px;
      li {
        display: flex;
        justify-content: space-between;
        align-items: center;
        span:nth-child(1) {
          font-size: 24px;
          line-height: 120%;
          font-weight: 600;
          color: #75767C;
          letter-spacing: 1.44px;
        }
        span:nth-child(2) {
          display: flex;
          justify-content: center;
          align-items: center;
          min-width: 290px;
          font-size: 20px;
          line-height: 120%;
          letter-spacing: 1.2px;
          background: #F0F0F4;
          border-radius: 5px;
          padding: 9px;
          margin-left: 12px;
        }
      }
      li:last-child {
        display: block;
        span:nth-child(2) {
          margin: 10px 0 0 0;
          padding: 15px;
        }
      }
    }
    &__info {
      margin-top: 20px;
      &-title {
        font-size: 24px;
        line-height: 120%;
        font-weight: 600;
        color: #75767C;
        letter-spacing: 1.44px;
      }
      &-text {
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 290px;
        font-size: 20px;
        line-height: 120%;
        letter-spacing: 1.2px;
        background: #F0F0F4;
        border-radius: 5px;
        padding: 15px;
        margin-top: 10px;
      }
    }
    &__text {
     p {
       font-size: 20px;
       line-height: 120%;
       letter-spacing: 1.2px;
     }
    }
    &__btn {
      align-items: center;
      cursor: pointer;
      margin-top: 30px;
      display: none;
      &-text {
        span {
          font-size: 16px;
          line-height: 120%;
          letter-spacing: 0.96px;
        }
        span:nth-child(2) {
          display: none;
        }
      }
      i {
        flex-shrink: 0;
        width: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: 8px;
        svg {
          width: 100%;
          height: auto;
        }
      }
      &--active {
        .about-product__btn-text {
          span:nth-child(1) {
            display: none;
          }
          span:nth-child(2) {
            display: block;
          }
        }
      }
    }
  }
  &-reviews {
    &__top {
      display: flex;
      align-items: center;
    }
    &__sorting {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      margin-top: 40px;
    }
    &__items {
      display: grid;
      grid-template-columns: 100%;
      gap: 20px;
      margin-top: 24px;
    }
    &__item {
      background: #F0F0F4;
      border-radius: 15px;
      padding: 24px;
      &-top {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
      &-group {
        display: flex;
        align-items: center;
      }
      &-title {
        font-size: 20px;
        line-height: 120%;
      }
      &-date {
        font-size: 16px;
        line-height: 120%;
        color: #686868;
        margin-left: 32px;
      }
      &-text {
        font-size: 18px;
        line-height: 120%;
        margin-top: 20px;
      }
    }
    .stars {
      margin: 0 0 0 12px;
    }
    &__btn {
      width: 290px;
      height: 64px;
      background: #F0F0F4;
      border-radius: 8px;
      margin: 50px auto 0;
      span {
        font-size: 20px;
        line-height: 120%;
      }
      &:hover {
        background: #d6d6de;
      }
    }
  }
  &-stars {
    display: flex;
    align-items: center;
    margin-left: 10px;
    &__view {
      display: flex;
      align-items: center;
    }
    &__item {
      width: 26px;
      svg {
        width: 100%;
        height: auto;
      }
    }
    &__rate {
      font-size: 30px;
      line-height: 105%;
      font-weight: 600;
      color: #EE842C;
      margin-left: 3px;
    }
    &__text {
      font-size: 20px;
      line-height: 120%;
      color: #75767C;
      margin-left: 20px;
    }
  }
  .sorting-text {
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
  }
  .sorting__wrap {
    display: flex;
    align-items: center;
    margin-left: 20px;
  }
  .sorting__links {
    display: flex;
    align-items: center;
  }
  .sorting__link {
    font-size: 16px;
    line-height: 120%;
    &:not(:first-child) {
      margin-left: 20px;
    }
    &:hover {
      color: #EE842C;
    }
    &--active {
      color: #EE842C;
    }
  }
  .sorting__checkbox {
    display: inline-flex;
    align-items: center;
    position: relative;
    margin-left: 20px;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      margin: 0;
      padding: 0;
      cursor: pointer;
      opacity: 0;
      &:checked ~ label {
        &:before {
          opacity: 1;
          pointer-events: all;
        }
      }
    }
    label {
      font-size: 16px;
      line-height: 120%;
      position: relative;
      padding-left: 36px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 26px;
        height: 26px;
        border: 1px solid #DFDFDF;
        border-radius: 5px;
      }
      &:before {
        content: '';
        position: absolute;
        z-index: 1;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 26px;
        height: 26px;
        background: #A31D1C;
        border: 1px solid #A31D1C;
        border-radius: 5px;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
    }
  }
  &-recipes {
    &__cards {
      display: grid;
      grid-template-columns: repeat(3, calc(100% / 3 - 90px / 3));
      gap: 45px;
    }
    &__card {
      display: flex;
      flex-direction: column;
      &:hover {
        .product-recipes__card-img {
          img {
            transform: scale(1.2);
          }
        }
      }
      &-img {
        position: relative;
        padding-bottom: 77%;
        overflow: hidden;
        img {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          border-radius: 15px;
          transition: all 0.5s;
        }
      }
      &-desc {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        margin-top: 20px;
      }
      &-title {
        font-size: 26px;
        line-height: 120%;
        letter-spacing: 1.3px;
        margin-bottom: auto;
      }
      &-info {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        margin-top: 20px;
      }
      &-rating,
      &-portion,
      &-time {
        display: flex;
        align-items: center;
        i {
          flex-shrink: 0;
          width: 32px;
          margin-right: 10px;
          svg {
            max-width: 100%;
            width: 100%;
            height: auto;
          }
        }
        span {
          font-size: 20px;
          line-height: 120%;
          letter-spacing: 1.2px;
        }
        &:hover {
          i svg path {
            fill: #A31D1C;
            stroke: #A31D1C;
          }
        }
      }
      &-rating {
        margin: 0 15px 10px 0;
      }
      &-portion {
        margin: 0 15px 10px 0;
      }
      &-time {
        margin: 0 0 10px 0;
      }
    }
    &__btn {
      width: 290px;
      height: 64px;
      background: #F0F0F4;
      border-radius: 8px;
      margin: 50px auto 0 auto;
      span {
        font-size: 20px;
        line-height: 120%;
        font-weight: 500;
      }
      &:hover {
        background: #d6d6de;
      }
    }
  }
  &-questions {
    &__form {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-radius: 8px;
      position: relative;
      input {
        width: 100%;
        height: 61px;
        background: #F0F0F4;
        border: 1px solid transparent;
        border-radius: 8px;
        font-size: 18px;
        line-height: 120%;
        padding: 5px 40px;
        &::placeholder {
          color: #A3A3A3;
        }
      }
    }
    &__btn {
      position: absolute;
      top: 0;
      right: 0;
      width: 248px;
    }
  }
  .question {
    font-size: 18px;
    line-height: 120%;
    margin-top: 15px;
  }
  .answer-title {
    font-size: 16px;
    line-height: 120%;
    color: #75767C;
    margin-top: 15px;
  }
  &-structure {
    &__wrap {
      display: flex;
      justify-content: space-between;
    }
    &__left {
      width: 567px;
      margin-right: 53px;
    }
    &__right {
      width: calc(100% - 620px);
      .product-structure__group {
        &:not(:first-child) {
          margin-top: 30px;
        }
      }
    }
    &__subtitle {
      font-size: 24px;
      line-height: 120%;
      color: #75767C;
      span {
        display: block;
        font-size: 20px;
        font-weight: 400;
      }
    }
    &__text {
      background: #F0F0F4;
      border-radius: 5px;
      padding: 16px 30px;
      font-size: 20px;
      line-height: 140%;
      margin-top: 24px;
    }
    &__items {
      display: grid;
      grid-template-columns: repeat(4, calc(100% / 4 - 90px / 4));
      gap: 30px;
      margin: 30px 0;
    }
    &__item {
      text-align: center;
      &-info {
        position: relative;
        margin: 0 auto;
        canvas {
          width: 115px !important;
          height: 115px !important;
        }
        span {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          font-size: 35px;
          line-height: 105%;
          font-weight: 500;
          color: #75767C;
        }
      }
      .circle {
        width: 115px;
        height: 115px;
        margin: 0 auto;
        position: relative;
      }
      &-desc {
        margin-top: 16px;
      }
      &-title {
        font-size: 18px;
        line-height: 120%;
        font-weight: 600;
        color: #75767C;
        letter-spacing: 1.08px;
        span:nth-child(2) {
          display: none;
        }
      }
      &-text {
        font-size: 18px;
        line-height: 120%;
        color: #75767C;
        margin-top: 3px;
        letter-spacing: 1.08px;
      }
    }
    &__bottom {
      margin-top: 30px;
      .product-structure__text {
        margin-top: 0;
      }
    }
    &__btn {
      align-items: center;
      cursor: pointer;
      margin-top: 30px;
      display: none;
      &-text {
        span {
          font-size: 16px;
          line-height: 120%;
          letter-spacing: 0.96px;
        }
        span:nth-child(2) {
          display: none;
        }
      }
      i {
        flex-shrink: 0;
        width: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: 8px;
        svg {
          width: 100%;
          height: auto;
        }
      }
      &--active {
        .about-product__btn-text {
          span:nth-child(1) {
            display: none;
          }
          span:nth-child(2) {
            display: block;
          }
        }
      }
    }
  }
}
.feedback {
  background-image: url("../img/feedback-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 50px 0;
  &__title {
    font-size: 40px;
    line-height: 137.5%;
    letter-spacing: 2.4px;
    span {
      color: #A31D1C;
    }
  }
  &__text {
    max-width: 685px;
    font-size: 18px;
    line-height: 140%;
    letter-spacing: 1.08px;
    margin-top: 20px;
  }
  &__form {
    margin-top: 30px;
    &-flex {
      display: flex;
      align-items: center;
      margin-bottom: 20px;
    }
    &-group {
      margin: 0;
      padding: 0;
      border: none;
      input {
        width: 368px;
        height: 67px;
        background: #FFF;
        border: 1px solid #ffffff;
        border-radius: 10px;
        padding: 5px 20px;
        font-size: 16px;
        line-height: 120%;
        &::placeholder {
          color: #75767C;
        }
      }
      textarea {
        width: 763px;
        height: 204px;
        background: #FFF;
        border: 1px solid #ffffff;
        border-radius: 10px;
        padding: 20px;
        font-size: 16px;
        line-height: 120%;
        resize: none;
        &::placeholder {
          color: #75767C;
        }
      }
    }
    .stars {
      margin-left: 40px;
      &__rate {
        font-size: 16px;
        color: #75767C;
        margin: 0 20px 0 0;
      }
    }
    &-btn {
      width: 340px;
      margin-top: 20px;
    }
    &-info {
      max-width: 346px;
      font-size: 14px;
      line-height: 140%;
      color: #75767C;
      margin-top: 15px;
      &:hover {
        a {
          color: #A31D1C;
        }
      }
    }
  }
}


@media screen and (max-width: 1400px) {
  .product {
    .weight-info {
      .hover-hide {
        left: unset;
        right: 0;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .product {
    &__left {
      width: 500px;
      margin-right: 30px;
    }
    &__right {
      width: calc(100% - 530px);
    }
    .tabs {
      .tab {
        width: 200px;
        span {
          font-size: 20px;
        }
      }
    }
    .about-product {
      &__left {
        width: 450px;
        margin-right: 30px;
      }
      &__right {
        width: calc(100% - 480px);
      }
      &__list {
        li {
          span:nth-child(1) {
            font-size: 18px;
          }
          span:nth-child(2) {
            min-width: 260px;
            font-size: 18px;
          }
        }
      }
      &__text {
        p {
          font-size: 16px;
        }
      }
    }
    &-recipes {
      &__cards {
        grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
        gap: 30px;
      }
    }
    &-structure {
      &__left {
        width: 450px;
        margin-right: 30px;
      }
      &__right {
        width: calc(100% - 480px);
      }
      &__items {
        grid-template-columns: repeat(2, calc(50% - 30px / 2));
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .product {
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
      margin: 0 0 30px 0;
    }
    &__right {
      width: 100%;
      padding-bottom: 80px;
      position: relative;
    }
    &__slider {
      &-btns {
        position: static;
      }
    }
    &-card {
      &__img {
        max-width: 300px;
      }
      &__btns {
        top: 15px;
        right: 15px;
        display: flex;
        align-items: center;
      }
      &__btn {
        width: 32px;
        height: 32px;
        &:not(:first-child) {
          margin: 0 0 0 7px;
        }
        svg {
          max-width: 15px;
        }
      }
      &__art {
        right: 15px;
        bottom: 15px;
      }
    }
    .swiper-button-prev {
      position: absolute;
    }
    .swiper-button-next {
      position: absolute;
    }
    &__title {
      font-size: 20px;
    }
    .stars {
      &__item {
        width: 19px;
      }
      &__rate {
        font-size: 16px;
      }
    }
    .weight {
      &-item {
        span {
          font-size: 14px;
        }
      }
    }
    &__price {
      margin-top: 10px;
      &-item {
        &:not(:last-child) {
          margin-right: 15px;
        }
      }
      &-number {
        font-size: 24px;
      }
      &-text {
        font-size: 16px;
      }
    }
    .plus-minus {
      button {
        width: auto;
        height: auto;
        border: unset;
        &:hover {
          background: #ffffff;
          color: #A31D1C;
        }
      }
      input {
        width: 70px;
        margin: 0 10px;
      }
    }
    &__btn {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
    }
    &__info2 {
      margin-top: 0;
      &-text {
        font-size: 14px;
      }
    }
    .tabs {
      .tab {
        width: auto;
        height: 50px;
        padding: 5px 20px;
        i {
          max-width: 20px;
        }
        span {
          font-size: 18px;
        }
      }
    }
    .about-product {
      &__left {
        width: 100%;
        max-width: 500px;
      }
      &__right {
        display: none;
      }
    }
    &-reviews {
      &__top {
        display: block;
      }
      &__title {
        font-size: 24px;
      }
      &__sorting {
        display: block;
      }
      &__item {
        padding: 20px 16px;
        &-date {
          font-size: 12px;
        }
        &-text {
          font-size: 16px;
        }
      }
      &__btn {
        height: 47px;
        margin-top: 30px;
        span {
          font-size: 16px;
        }
      }
    }
    &-stars {
      margin: 5px 0 0 0;
      &__item {
        width: 17px;
      }
      &__rate {
        font-size: 20px;
      }
      &__text {
        font-size: 14px;
        margin-left: 12px;
      }
    }
    .sorting__wrap {
      margin: 20px 0 0 0;
    }
    .sorting-text {
      font-size: 18px;
      font-weight: 500;
    }
    .sorting__link {
      font-size: 14px;
      &:not(:first-child) {
        margin-left: 12px;
      }
    }
    .sorting__checkbox {
      margin-left: 12px;
      label {
        font-size: 14px;
        padding-left: 30px;
        &:after {
          width: 20px;
          height: 20px;
        }
        &:before {
          width: 20px;
          height: 20px;
        }
      }
    }
    &-recipes {
      &__cards {
        grid-template-columns: repeat(3, calc(50% - 30px / 2));
        gap: 30px;
      }
      &__card {
        &-title {
          font-size: 20px;
        }
        &-rating,
        &-portion,
        &-time {
          i {
            width: 26px;
          }
          span {
            font-size: 16px;
          }
        }
      }
      &__btn {
        height: 47px;
        margin-top: 30px;
        span {
          font-size: 16px;
        }
      }
    }
    .question {
      font-size: 16px;
    }
    &-structure {
      &__wrap {
        display: block;
      }
      &__left {
        width: 100%;
        margin: 0;
      }
      &__right {
        width: 100%;
        margin-top: 30px;
      }
      &__subtitle {
        font-size: 16px;
        span {
          font-size: 16px;
        }
      }
      &__text {
        font-size: 18px;
        padding: 16px 12px;
        margin-top: 16px;
      }
      &__items {
        grid-template-columns: repeat(4, calc(100% / 4 - 30px / 4));
        gap: 10px;
      }
      &__item {
        &-info {
          span {
            font-size: 20px;
          }
        }
        .circle {
          width: 78px;
          height: 78px;
          canvas {
            width: 78px !important;
            height: 78px !important;
          }
        }
        &-desc {
          margin-top: 12px;
        }
        &-title {
          font-size: 14px;
        }
        &-text {
          font-size: 14px;
          margin-top: 8px;
        }
      }
    }
  }
  .feedback {
    padding: 30px 0;
    &__title {
      font-size: 28px;
    }
    &__text {
      max-width: 600px;
      font-size: 16px;
      margin-top: 10px;
    }
    &__form {
      &-flex {
        flex-direction: column;
        align-items: start;
        margin-bottom: 16px;
      }
      &-group {
        input {
          height: 54px;
        }
        textarea {
          width: 600px;
          height: 150px;
        }
      }
      .stars {
        order: -1;
        margin: 0 0 16px 0;
      }
      &-btn {
        margin-top: 30px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .feedback {
    &__form {
      &-group {
        width: 100%;
        input {
          width: 100%;
        }
        textarea {
          width: 100%;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .product {
    .about-product {
      &__list {
        li {
          display: inline-flex;
          flex-direction: column;
          align-items: start;
          span:nth-child(2) {
            min-width: unset;
            width: auto;
            margin: 10px 0 0 0;
          }
          &:nth-child(2) ~ li {
            display: none;
          }
        }
        &--active {
          li {
            display: inline-flex !important;
          }
        }
      }
      &__btn {
        display: inline-flex;
      }
    }
    &-reviews {
      &__item {
        &-title {
          font-size: 18px;
        }
      }
      &__btn {
        width: 100%;
      }
      .stars {
        &__item {
          width: 12px;
        }
      }
    }
    &-recipes {
      &__cards {
        grid-template-columns: 100%;
      }
      &__btn {
        width: 100%;
      }
    }
    &-structure {
      &__right {
        display: none;
        &--active {
          display: block;
        }
      }
      &__bottom {
        display: none;
        &--active {
          display: block;
        }
      }
      &__item {
        &-title {
          span:nth-child(1) {
            display: none;
          }
          span:nth-child(2) {
            display: block;
          }
        }
      }
      &__btn {
        display: inline-flex;
      }
    }
  }
  .feedback {
    &__form {
      &-btn {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 360px) {
  .product {
    .sorting__wrap {
      display: block;
    }
    .sorting__checkbox {
      margin: 12px 0 0 0;
    }
    &-structure {
      &__items {
        grid-template-columns: repeat(2, calc(50% - 15px / 2));
        gap: 15px;
      }
    }
    &-reviews {
      &__item {
        &-top {
          display: block;
        }
        &-date {
          margin: 12px 0 0 0;
        }
      }
    }
  }
}