.offers {
  padding: 0 0 25px 0;
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 30px / 4));
    gap: 10px;
    margin-top: 30px;
  }
  &__item {
    text-align: center;
    background: #F9F9F9;
    border-radius: 10px;
    padding: 22px;
    &-icon {
      max-width: 35px;
      margin: 0 auto;
      img {
        max-width: 100%;
      }
    }
    &-desc {
      margin-top: 6px;
    }
    &-title {
      font-size: 20px;
      line-height: 140%;
    }
    &-text {
      font-size: 16px;
      line-height: 140%;
      color: #6C6C6C;
      margin-top: 6px;
    }
  }
  &__img {
    grid-column: 1/3;
    grid-row: 1/3;
    img {
      width: 100%;
      height: 100%;
      border-radius: 10px;
    }
  }
  &__download {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F9F9F9;
    border-radius: 10px;
    padding: 22px;
    &-desc {
      display: inline-flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      i {
        width: 40px;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 18px;
        line-height: 120%;
        margin-top: 8px;
      }
    }
  }
}
.connection {
  padding: 25px 0 0 0;
  &__form {
    margin-top: 34px;
    &-grid {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
      gap: 15px;
    }
    &-group {
      margin: 0;
      padding: 0;
      border: none;
      input {
        width: 100%;
        height: 53px;
        background: #ffffff;
        border: 1px solid #DFDFDF;
        border-radius: 5px;
        padding: 5px 15px 5px 30px;
        font-size: 16px;
        line-height: 120%;
        &::placeholder {
          color: #75767C;
        }
      }
      textarea {
        width: 100%;
        height: 183px;
        background: #ffffff;
        border: 1px solid #DFDFDF;
        border-radius: 5px;
        padding: 15px 15px 15px 30px;
        font-size: 16px;
        line-height: 120%;
        resize: none;
        &::placeholder {
          color: #75767C;
        }
      }
    }
    .big {
      grid-column: 1/3;
    }
    &-info {
      display: inline-flex;
      position: relative;
      margin-top: 15px;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
        opacity: 0;
        cursor: pointer;
        &:checked ~ label {
          &:before {
            opacity: 1;
            pointer-events: all;
          }
        }
      }
      label {
        font-size: 14px;
        line-height: 120%;
        color: #75767C;
        padding-left: 28px;
        a {
          position: relative;
          z-index: 3;
          &:hover {
            color: #A31D1C;
          }
        }
        &:after {
          content: '';
          position: absolute;
          top: -2px;
          left: 0;
          width: 20px;
          height: 20px;
          border: 1px solid #DFDFDF;
          border-radius: 5px;
        }
        &:before {
          content: '';
          position: absolute;
          z-index: 2;
          top: 1px;
          left: 3px;
          width: 14px;
          height: 14px;
          background: #A31D1C;
          border-radius: 3px;
          opacity: 0;
          pointer-events: none;
          transition: all 0.3s;
        }
      }
    }
    &-btn {
      width: 100%;
      margin-top: 15px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .offers {
    &__items {
      grid-template-columns: repeat(3, calc(100% / 3 - 20px / 3));
    }
    &__item {
      order: -1;
    }
    &__img {
      order: -2;
    }
  }
}

@media screen and (max-width: 992px) {
  .offers {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
    }
    &__item {
      &-icon {
        max-width: 32px;
      }
      &-title {
        font-size: 18px;
      }
      &-text {
        font-size: 14px;
      }
    }
    &__img {
      grid-column: 1;
      grid-row: 1;
    }
  }
  .connection {
    &__form {
      &-group {
        input {
          padding: 5px 15px;
        }
        textarea {
          height: 160px;
          padding: 15px;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .offers {
    &__items {
      grid-template-columns: 100%;
      gap: 16px;
    }
    &__item {
      &-desc {
        max-width: 290px;
        margin: 0 auto;
      }
    }
    &__download {
      &-desc {
        max-width: 290px;
        margin: 0 auto;
      }
    }
  }
  .connection {
    &__form {
      background: #F0F0F4;
      border-radius: 24px;
      padding: 24px;
      &-grid {
        grid-template-columns: 100%;
      }
      &-group {
        input {
          border-radius: 10px;
        }
        textarea {
          border-radius: 10px;
        }
      }
      .big {
        grid-column: 1;
      }
    }
  }
}