.flex-center {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
}

.column {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.img-fit {
  position: absolute;
  width: 100%;
  height: 100%;
  -o-object-position: center center;
  object-position: center center;
  -o-object-fit: cover;
  object-fit: cover;
  display: block;
  left: 0;
  top: 0;
  height: 100%;
}

:root {
  font-size: 1em;
  scroll-behavior: smooth;
  --fontBody: 'gilroy';
  --fontHeader: 'Unbounded';
  --colorGray: #6C6C6C;
  --colorGrayText: #75767C;
  --colorGreen: #64AD01;
  --colorPurple: #A31D1C;
  --colorPurpleHover: #851818;
  --colorBg: #F0F0F4;
  --colorBgGray: #949494;
  --colorBorder: #E3E3E8;
}

@media (max-width: 1299px) {
  body {
    padding-bottom: 64px;
  }
}

a:hover {
  color: var(--colorPurple);
}

label {
  display: block;
  cursor: pointer;
}

input {
  background: transparent;
  padding: 0;
  font-family: inherit;
  font-size: inherit;
  color: inherit;
}

input:focus {
  outline: none;
}

textarea {
  outline: none !important;
  font-family: inherit;
  font-size: inherit;
}

[type=submit],
button {
  background: transparent;
  border: none;
  cursor: pointer;
  transition: .3s;
}

button {
  padding: 0;
  font-family: inherit;
  outline: none !important;
}

::-moz-placeholder {
  opacity: 1;
}

::placeholder {
  opacity: 1;
}

mark {
  background: transparent;
  color: currentColor;
}

h2 {
  font: 3rem/1.2 var(--fontHeader);
}

.container>h2 {
  margin-bottom: 30px;
}

@media (max-width: 1199px) {
  h2 {
    font-size: 2.25rem;
  }
}

@media (max-width: 991px) {
  h2 {
    font-size: 1.875rem;
  }
}

@media (max-width: 767px) {
  h2 {
    font-size: 1.25rem;
  }
}

.btn,
.btn-red,
.btn-gray {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
  border-radius: 8px;
  height: 61px;
  font-size: 1.25rem;
  font-weight: 600;
  width: 100%;
}

@media (max-width: 767px) {

  .btn,
  .btn-red,
  .btn-gray {
    font-size: 1rem;
    height: 55px;
  }
}

.btn-red {
  color: #fff;
  background: var(--colorPurple);
  box-shadow: 1px 0px 10px 0px rgba(112, 112, 112, 0.15);
  padding: 0 15px;
}

.btn-red:disabled {
  background: var(--colorBg);
  color: #000;
}

.btn-red:hover {
  color: #fff;
  background: var(--colorPurpleHover);
}

.btn-gray {
  font: 400 1rem/1.2 var(--fontBody);
  padding: 0 15px;
  background: var(--colorBg);
}

.purple {
  color: var(--colorPurple) !important;
}

.container {
  width: 100%;
  padding-left: calc(50% - 640px);
  padding-right: calc(50% - 640px);
}

@media (max-width: 1312px) {
  .container {
    padding-left: 16px;
    padding-right: 16px;
  }
}

.form-control {
  background: #fff;
  border-radius: 5px;
  border: 1px solid #DFDFDF;
  height: 54px;
  padding: 0 20px;
  display: block;
  width: 100%;
}

.form-control::-moz-placeholder {
  color: var(--colorGrayText);
}

.form-control::placeholder {
  color: var(--colorGrayText);
}

textarea.form-control {
  padding: 18px 20px;
}

.row-span-2 {
  grid-row: span 2;
}

.column-span-2 {
  grid-column: span 2;
}

section.container {
  margin-top: 50px;
}

.logo {
  display: grid;
  gap: 13px;
  grid-template-columns: max-content min-content;
  color: #A41E1D;
  letter-spacing: 0.625px;
  font: 600 1.5625rem/1 var(--fontHeader);
  align-items: center;
  flex-shrink: 0;
}

.plus-minus {
  display: grid;
  grid-template-columns: max-content 1fr max-content;
  gap: 5px;
  justify-content: end;
}

.plus-minus button {
  color: var(--colorPurple);
  border: 2px solid currentColor;
  transition: .3s;
  line-height: 1;
}

.plus-minus button:hover {
  color: #fff;
  background: var(--colorPurple);
  border-color: var(--colorPurple);
}

.plus-minus input {
  border: none;
  text-align: center;
  transition: opacity .3s;
  width: 100%;
}

.offer .plus-minus {
  margin-top: 9px;
}

.offer .plus-minus button {
  border-radius: 28px;
  font-size: 2.5rem;
  width: 52px;
  height: 52px;
}

.offer .plus-minus input {
  font-size: 1.25rem;
  width: 100%;
}

@media (max-width: 767px) {
  .offer .plus-minus {
    margin-top: 2px;
  }

  .offer .plus-minus button {
    width: 25px;
    height: 25px;
    font-size: 1.25rem;
    line-height: 1;
  }

  .offer .plus-minus input {
    font-size: 0.625rem;
  }
}

.plus-minus--hidden input,
.plus-minus--hidden button:first-of-type {
  opacity: 0;
  pointer-events: none;
}

.section-header {
  display: flex;
  align-items: baseline;
  margin-bottom: 34px;
  gap: 16px;
}

.section-header .flex-center {
  gap: 16px;
}

.section-header a:not(.flex-center) {
  font: 1.125rem/1.2 var(--fontHeader);
  letter-spacing: 0.45px;
  color: var(--colorBgGray);
  margin-left: auto;
}

.section-header a:not(.flex-center):hover {
  color: #A31D1C;
}

@media (max-width: 991px) {
  .section-header .ico-orange {
    width: 36px;
    height: 36px;
  }

  .section-header a:not(.flex-center) {
    font-size: 1rem;
  }
}

@media (max-width: 767px) {
  .section-header {
    gap: 9px;
    align-items: center;
    margin-bottom: 15px;
  }

  .section-header .flex-center {
    gap: 9px;
  }

  .section-header .ico-orange {
    width: 20px;
    height: 20px;
  }

  .section-header a {
    font-size: 0.625rem;
  }
}

.ico-orange {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
  width: 47px;
  height: 47px;
  background: url(../img/h2-ico-bg.webp) no-repeat;
  background-size: contain;
  color: #fff;
  margin: 0;
  img {
    width: 100%;
    height: auto;
  }
}

.ico-circle {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
  width: 40px;
  height: 41px;
  background: url(../img/ico-bg-orange.webp) no-repeat;
  background-size: contain;
  color: #fff;
  img {
    width: 100%;
  }
}

.product-line {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 24px;
}

.menu-categories {
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 72px / 4));
  gap: 24px;
}

.menu-categories a {
  position: relative;
  display: block;
  min-height: 286px;
  padding: 21px 35px 0;
  overflow: hidden;
  border-radius: 20px;
}

.menu-categories a p {
  position: relative;
  z-index: 1;
  font-weight: 600;
  font-size: 35px;
  color: #fff;
}

.menu-categories a p:hover~.img-fit {
  transform: scale(1.1);
}

.menu-categories a .img-fit {
  transition: .3s;
}

.menu-categories a:hover .img-fit:hover {
  transform: scale(1.05);
}

.menu-categories a:last-child {
  display: none;
}

@media screen and (min-width: 1300px) {
  .menu-categories.collapse__content {
    display: none;
  }
}

.menu-categories__hide {
  display: none !important;
  &--active {
    display: grid !important;
  }
}

@media (max-width: 1299px) {
  .menu-categories {
    grid-template-columns: 1fr 1fr 1fr;
    gap: 8px;
    margin-top: 8px;
  }

  .menu-categories a {
    grid-column: initial;
    grid-row: initial;
    min-height: 0;
    padding: 100% 0 0;
  }

  .menu-categories a p {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 16px 30px 4px;
    font-size: 1.875rem;
  }

  .menu-categories a:last-child {
    display: block;
  }

  .menu-categories.collapse__content {
    margin-top: 8px;
  }
}

@media (max-width: 767px) {
  .menu-categories a p {
    font-size: 1.25rem;
    padding-left: 8px;
    padding-right: 8px;
  }
}

@media (max-width: 575px) {
  .menu-categories a p {
    font-size: 0.875rem;
  }
}

@media screen and (max-width: 360px) {
  .menu-categories {
    grid-template-columns: 1fr 1fr;
  }
}

.dropdown-hover {
  position: relative;
  z-index: 11;
}

@media (max-width: 1299px) {
  .dropdown-hover {
    z-index: auto;
  }
}

.dropdown-hover__btn {
  cursor: pointer;
}

.dropdown-hover__content {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  transition: .3s;
  opacity: 0;
  pointer-events: none;
  border-radius: 8px;
  background: #FFF;
  box-shadow: 1px 0px 10px 0px rgba(112, 112, 112, 0.15);
  z-index: 200;
}

.dropdown-hover:hover .dropdown-hover__content {
  opacity: 1;
  pointer-events: auto;
}

.dropdown-hover__caret {
  transition: .3s;
  transform: rotate(-90deg);
}

.dropdown-hover:hover .dropdown-hover__caret {
  transform: rotate(90deg);
}

.collapse__btn {
  cursor: pointer;
  transition: .3s;
}

.collapse__caret {
  transition: .3s;
  flex-shrink: 0;
  transform: none;
}

.open>.collapse__btn .collapse__caret {
  transform: rotate(180deg);
}

[data-value-to-insert] {
  cursor: pointer;
}

.insert-selected {
  transition: .3s;
  opacity: 0;
}

[data-value-to-insert]:hover .insert-selected,
[data-value-to-insert].selected .insert-selected {
  opacity: 1;
}

.sots {
  display: flex;
  flex-wrap: wrap;
}

.sots p {
  width: 100%;
}

.sots svg {
  width: 100%;
  height: auto;
  display: block;
}

[data-tabs] {
  cursor: pointer;
  transition: .3s;
}

[data-tabs-content] {
  transition: .4s;
  opacity: 0;
}

[data-tabs-content].active {
  opacity: 1;
}

[data-tabs-content]:not(.active) {
  display: none;
}

.stars {
  display: grid;
  grid-template-columns: repeat(5, max-content);
  gap: 2px;
  color: var(--colorPurple);
}

.stars--four svg:nth-child(n+5) {
  color: #B8B8B8;
}

.stars--three svg:nth-child(n+4) {
  color: #B8B8B8;
}

.stars--two svg:nth-child(n+3) {
  color: #B8B8B8;
}

.stars--one svg:nth-child(n+2) {
  color: #B8B8B8;
}

.stars-select {
  display: flex;
  align-items: center;
  gap: 16px;
}

.stars-select span {
  font: 400 1rem/1.125 var(--fontBody);
  color: var(--colorGrayText);
}

.stars-select__number {
  color: var(--colorGrayText);
  display: flex;
  gap: 10px;
  flex-direction: row-reverse;
}

.stars-select__number input {
  display: none;
}

.stars-select__number input:checked~label {
  color: var(--colorPurple);
}

.stars-select__number label {
  color: #B8B8B8;
}

.stars-select__number svg {
  display: block;
  transition: .3s;
}

.info {
  position: relative;
  cursor: pointer;
}

.info svg {
  display: block;
}

.info__tooltip {
  position: absolute;
}

.info__tooltip {
  font: 400 0.875rem/1.343 var(--fontBody);
  position: absolute;
  top: calc(100% + 14px);
  left: 50%;
  transform: translateX(-50%);
  border-radius: 7px;
  background: #FFF;
  filter: drop-shadow(1px 0px 10px rgba(112, 112, 112, 0.15));
  padding: 6px 12px 5px;
  pointer-events: none;
  transition: .3s;
  opacity: 0;
}

.info__tooltip::before {
  border-bottom: 8px solid #fff;
  border-left: 2.5px solid transparent;
  border-right: 2.5px solid transparent;
  content: '';
  position: absolute;
  bottom: calc(100% - 2px);
  left: 50%;
  transform: translateX(-50%);
}

.info:hover .info__tooltip {
  opacity: 1;
}

.two-icons>* {
  position: relative;
  margin: 0;
}

.two-icons>*:hover svg+svg {
  opacity: 1;
}

.two-icons svg+svg {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  transition: .3s;
}

.two-icons .header-btn svg+svg {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.two-icons--filled svg+svg {
  opacity: 1;
}

@media screen and (min-width: 1300px) {
  .show-mobile {
    display: none !important;
  }
}

.link-dotted {
  -webkit-text-decoration: 1px dotted underline;
  text-decoration: 1px dotted underline;
}

.link-dotted:hover {
  text-decoration-color: transparent;
  color: inherit;
}

.link-underline {
  text-decoration: underline;
}

.link-dotted-underline {
  -webkit-text-decoration: 1px dotted underline;
  text-decoration: 3px dotted underline;
  text-decoration-color: transparent;
}

.link-dotted-underline:hover {
  text-decoration-color: currentColor;
  text-underline-offset: 4px;
  color: inherit;
}

.swiper {
  margin: 0;
}

.swiper,
.swiper * {
  box-sizing: border-box;
}

.swiper-button-prev,
.swiper-button-next {
  color: var(--colorPurple);
}

.swiper-button-prev::after,
.swiper-button-next::after {
  display: none;
}

.swiper-button-prev:hover,
.swiper-button-next:hover {
  color: #df512d;
}

.fancybox__backdrop {
  background: rgba(0, 0, 0, 0.3);
}

.fancybox-close {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
}

.fancybox__slide {
