.c-hamburger {
  display: none;
  position: relative;
  width: 17px;
  height: 15px;
  padding: 0;
  cursor: pointer;
  transition: background 0.3s;
  z-index: 1;
  align-self: center;
  flex-shrink: 0;
}

@media (max-width: 1299px) {
  .c-hamburger {
    display: block;
  }
}

.c-hamburger:focus {
  outline: none;
}

.c-hamburger span {
  position: absolute;
  top: 7px;
  left: 0;
  right: 0;
  height: 2px;
  background: #000;
  pointer-events: none;
}

.c-hamburger span::before,
.c-hamburger span::after {
  position: absolute;
  left: 0;
  right: 0;
  height: 2px;
  background-color: #000;
  content: "";
}

.c-hamburger span::before {
  top: -7px;
}

.c-hamburger span::after {
  bottom: -7px;
}

.c-hamburger--htx span::before,
.c-hamburger--htx span::after {
  transition-duration: 0.3s, 0.3s;
  transition-delay: 0.3s, 0s, 0s;
}

.c-hamburger--htx span::before {
  transition-property: top, transform;
}

.c-hamburger--htx span::after {
  transition-property: bottom, transform;
}

/*  active     */
.c-hamburger--htx.is-active span {
  background: none;
}

.c-hamburger--htx.is-active span::before {
  top: 0;
  transform: rotate(45deg) scale(1.2);
  background: #000;
}

.c-hamburger--htx.is-active span::after {
  bottom: 0;
  transform: rotate(-45deg) scale(1.2);
  background: #000;
}

.c-hamburger--htx.is-active span::before,
.c-hamburger--htx.is-active span::after {
  transition-delay: 0s, 0.3s, .3s;
  left: 0;
  right: 0;
}

